<template>
  <div class="homeDiv">
   <!-- <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>-->
    <mu-container ref="container" class="demo-loadmore-content">
    <!--  <mu-bottom-sheet :open.sync="open">
        <div class="btnBar"><div class="fixWidth"><div class="cancel" @click="closeBottomSheet">取消</div><div class="title">选择时间</div><div class="ensure" @click="sureBottomSheet">确认</div></div></div>
        <mu-flex justify-content="center" align-items="start" wrap="wrap">
          <mu-paper :z-depth="1">
            <mu-time-picker color="tealA700" :no-display="!display" :view-type="viewType" :time.sync="time" :format="type" :landscape="landscape"></mu-time-picker>
          </mu-paper>
        </mu-flex>
      </mu-bottom-sheet>-->
      <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
          <div class="xywz_top">
            <div class="find clearfix">
              <div class="find_doc set_box_shadow">
                <div class="find_docDiv" @click="toChat(i.bodyId,i.bodyVersion,i.patientName)" v-for="(i,index) in list"><img src="../img/myLogo.png">
                  <span class="new" v-if="i.see == false"></span>
                  <div class="btm"><p><span class="black">{{i.patientName}}</span><span
                    class="lastTime">{{i.newSendDatetime | FormatDataTime}}</span></p>
                    <p class="contentNews">{{i.newChatMsg}}</p></div>
                </div>
              </div>
            </div>
          </div>
          <!--<div class="hos_child caseDiv set_box_shadow" v-for="i,index in list">
            <div class="hos_list_top clearfix">
              <div class="orderContent">
                <div class="left" style=""><img src="https://www.enuo120.com/upload/image/201806/05/HfgFPMKo1MVTEvAEkk4.png"></div>
                <div class="right"><p class="detail"><span class="name" style="font-size: 1.05rem">{{i.medicalInstitution}}</span>
                </p>
                  <p class="detail" style="line-height: 1rem;color: #999;    height: auto;"><span
                    class="name overflow_num_ellipsis">{{i.content}}</span></p>
                  <p class="orderdetailchoose" style="margin-bottom: .5rem;color: #999"><span>就诊时间：</span><span class="orderdetailchooseName">{{i.createDate | FormatData}}</span>
                    <button class="orderReadMore"  @click="readmore(i.id)">查看更多&gt;</button>
                  </p>
                </div>
              </div>
            </div>
          </div>-->
      </mu-load-more>
    </mu-container>
  </div>

</template>
<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError,formatDate} from "../assets/js/assist";
  import Vue from 'vue';
  export default {
      name:'chatList',
      data: function () {
        return {
          maximumPressure:'',
          minimumPressure:'',
          heartRate:'',
          monitoringDatetime:'',
          open: false,
          time: undefined,
          landscape: false,
          display: false,
          type: '24hr',
          viewType: 'list',
          chooseDaytime:'',
          choosetime:'',
          list:[],
          intimeNewList:null,
          refreshing: false,
          loading: false,
          insert_chart:'insert_chart',
          selects:true,
          searchVal:'',
          pageNumber:1,
          isFirstEnter:false // 是否第一次进入，默认false
        }
      },
      methods: {
        addXueya () {
          let elThis = this;
          this.monitoringDatetime = this.chooseDaytime +' '+ this.choosetime + ':00';
          let data = {};
          data.userId = this.$route.query.id;
          data.maximumPressure = this.maximumPressure;
          data.minimumPressure = this.minimumPressure;
          data.heartRate = this.heartRate;
          data.monitoringDatetime = this.monitoringDatetime;
          console.log(data)
          post(this.SEVERADDR + '/health_supervisor/bloodPressure/addBloodPressure', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            if(res.success == true){
              this.$toast.success('添加成功');
            }else{
              this.$toast.error(res.data);
            }
            //this.$router.go(0);
            //elThis.loading1 = !elThis.loading1;
           // window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getList(pageNumber){
          let elThis = this;
          let data = {};
          data.pageNumber = pageNumber;
          data.pageSize = 10;
          get(this.SEVERADDR + '/health_supervisor/hs_new_msg_see/all/body', {params:data}).then((res) => {
            console.log(res);
            if(res.data.length > 0){
              res.data.forEach(function (value) {
                elThis.list.push(value);
              })
            }
            elThis.refreshing = false;
            elThis.loading = false;
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getNewMsgList(){
          let elThis = this;
          let data = '';
          get(this.SEVERADDR + '/health_supervisor/hs_new_msg_see/new/body', {params:data}).then((res) => {
            if(res.data.length > 0){
              res.data.forEach(function (value) {
                elThis.list.forEach(function (valuehave) {//往现有的list去一个个比较
                  if(valuehave.id == value.id){
                    valuehave.bodyId = value.bodyId;
                    valuehave.bodyVersion = value.bodyVersion;
                    valuehave.newChatManName = value.newChatManName;
                    valuehave.newChatMsg = value.newChatMsg;
                    valuehave.newSendDatetime = value.newSendDatetime;
                    valuehave.see = value.see;
                  }
                });
                //elThis.list.push(value);

              })
            }
            elThis.refreshing = false;
            elThis.loading = false;
          }).catch(function(err){
            ajaxError(err);
          })
        },
        refresh () {   //刷新，重新清空数组
          this.refreshing = true;
          this.pageNumber = 1;
          this.list = [];
          this.getList(this.pageNumber);
          this.$refs.container.scrollTop = 0;
        },
        load () {  //继续加载，push
          this.loading = true;
          this.pageNumber++;
          this.getList(this.pageNumber);
        },
        caozuoWarn(index){
          console.log(index)
          if(index % 2 == 0){

          }
        },
        patientDetail(id,name){
          this.$router.push({
            path: '/patientDetail',
            query: {id: id,name:name}
          })
        },
        addJiuzhenBingli(){
          this.$router.push({
            path: '/postPhotos',
            query: {id: this.$route.query.id,name:this.$route.query.name}
          })
        },
        sureBottomSheet(){
          this.open = false;
          if(this.time != undefined){
            this.choosetime = formatDate(this.time,'HH:mm');
          }
        },
        closeBottomSheet () {
          this.open = false;
        },
        openBotttomSheet () {
          this.open = true;
        },
        toChat(id,version,name){
          let ID = id;
          let VERSION = version;
          let NAME = name;
          this.$router.push({
            path: '/chat',
            query: {id:ID,veision:VERSION,name:NAME}
          })
        },
        toClearChat(){
          this.$emit('toClearChat', this.intimeNewList)
        }
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'home'){
        to.meta.isBack = false;//要请求的，
      }else{
        to.meta.isBack = true;//是返回的 不需要请求数据，用缓存的
      }
      next();
    },
  /*  beforeRouteLeave(to, from, next) {
      console.log('取消的定时器'+this.intimeNewList)
      clearInterval(this.intimeNewList);　　// 清除定时器
      //this.intime = null;
      next();
    },*/
    activated(){
        let elThis = this;
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        this.choosetime = formatDate(new Date(), 'HH:mm');
        this.chooseDaytime = formatDate(new Date(), 'yyyy-MM-dd');
        this.list = [];
        this.pageNumber = 1;
        this.getList(this.pageNumber);
      }
      clearInterval(elThis.intimeNewList);  //需要每次进来都请求
      this.intimeNewList = setInterval(function () {
        elThis.getNewMsgList();
        elThis.toClearChat();
        console.log('子组件的值'+elThis.intimeNewList)
      },2000)
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter = false;
    },
      // 页面初始化
      created() {
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      }
    }
  Vue.filter('FormatDataTime', function (value) {
    return  formatDate(new Date(value),'yyyy-MM-dd hh:mm:ss');
  })
</script>
<style scoped>
  .clear{
    clear: both;
  }
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #f1f1f1;
  }

.mescroll{
  border: 1px solid transparent;
  padding: 0;
  float: left;
  width: 94.5%;
}
  .find_docDiv {
    height: 3.7rem;
    position: relative;
    border-bottom: 1px solid #eeeeee;
    background-color: white;
    margin-left: 3%;
    width: 100%;
  }
  .xywz_top .find .find_doc{
    /* height: 11.2rem; */
    position: relative;
    width: 100%;
    padding: .3rem .2rem;
  }
  .set_box_shadow {
    width: 94%;
    position: relative;
    /* margin: 0 auto; */
/*    margin-left: 3%;*/
    margin-top: .6rem;
    border-radius: 5px;
    background-color: #ffffff;
    /* box-shadow: 0px 6px 20px 0 rgba(127, 127, 127, 0.19); */
    font-family: PingFang-SC;
    padding: 0 .2rem;
  }
  .xywz_top .find .find_doc img{
    /* width: 38%; */
    margin: .5rem 0 0 1%;
    max-height: 2.6rem;
    width: initial;
  }
  .find .find_doc .btm {
    width: 80%;
    height: 3rem;
    font-size: .85rem;
    /* border: 1px solid red; */
    margin: 0 0 0 0;
    line-height: 1.5rem;
    position: absolute;
    top: .5rem;
    left: 17%;
  }
  .find .find_doc .btm p{
    margin: 0;
  }
  .find div .black {
    height: 1.25rem;
    line-height: 1.25rem;
    font-size: 1rem;
    font-weight: 600;
  }
  .lastTime {
    float: right;
    font-size: .8rem;
    color: #555;
     margin-right: 1rem;
  }
  .contentNews {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
    color: #666;
  }
  .new {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #ff9900;
    position: absolute;
    top: .4rem;
    left: -.4rem;
  }
</style>



