<template>

      <div class="demo-text">
        <div class="homeListTabOne">
          <div class="head">
            <img :src="userpic">
          </div>
          <div style="text-align: center;margin-bottom: .5rem">
            <span class="username">{{username}}</span>
            <span class="sexImg"><img class="sexImgSrc"  src="../img/healthMan.png"></span>
            <span class="age">{{age}}</span>
          </div>
          <div style="text-align: center;">
            <span style="margin-right: 18px">所属医生：<span>{{doctor}}</span></span>
            <span>所属护士：<span>{{nurse}}</span></span>
          </div>
        </div>
        <div class="homeList" style="padding: 10px 2%;float: left;width: 100%;    margin-top: .7rem;">
          <div class="homeService" @click="tohrefBasePage('/patientDetailBaseInfo')">
            <img class="homeServiceCenterImg" src="../img/myInfo.png"><span class="homeServiceCenterZi">个人基本信息</span>
            <p class="homeServiceRight"><span class="percent">{{personalInformationPercent}}</span><span>%</span></p>
          </div>
          <div style="margin-right: 0" class="homeService" @click="tohrefBasePage('/patientDetailHealthInfo')">
            <img class="homeServiceCenterImg" src="../img/myhealthInfo.png"><span class="homeServiceCenterZi">基础健康信息</span>
            <p class="homeServiceRight"><span class="percent">{{basicInfoPercent}}</span><span>%</span></p>
          </div>
          <div class="homeService" @click="tohrefBasePage('/patientDetailLife')">
            <img class="homeServiceCenterImg" src="../img/myLifeInfo.png"><span class="homeServiceCenterZi">个人生活方式</span>
            <p class="homeServiceRight"><span class="percent">{{userLifePercent}}</span><span>%</span></p>
          </div>
          <div style="margin-right: 0" class="homeService" @click="tohrefBasePage('/patientBodyInfo')">
            <img class="homeServiceCenterImg" src="../img/myAnalyseInfo.png"><span class="homeServiceCenterZi">个人体征数据</span>
            <p class="homeServiceRight"><span class="percent">{{userTraitInfoPercent}}</span><span>%</span></p>
          </div>
          <div  class="homeService" @click="topatientDetailCheck">
            <img class="homeServiceCenterImg" src="../img/myMeterInfo.png"><span class="homeServiceCenterZi">日常指标监测</span>
            <!--<p class="homeServiceRight"><span class="percent">60</span><span>%</span></p>-->
          </div>

          <div style="margin-right: 0" class="homeService" @click="toQiyue">
            <img class="homeServiceCenterImg" src="../img/contractService.png"><span class="homeServiceCenterZi">签约服务</span>
            <!--<p class="homeServiceRight"><span class="percent">60</span><span>%</span></p>-->
          </div>
        </div>
      </div>

</template>
<script>
  import {get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";

  export default {
      name:'patientDetailBase',
      // 组件
      props:["userpic","doctor","nurse","username","sex","age","personalInformationPercent","basicInfoPercent","userLifePercent","userTraitInfoPercent","id"], //父组件：doctor
      data: function () {
        return {

        }
      },
      methods: {
        topatientDetailCheck(){
          this.$emit('activeChange','3')
        },
        toReport(){
          this.$emit('activeChange','1')
        },
        toQiyue(){
          this.$toast.info('即将开放，敬请期待');
        },
        tohrefBasePage(page){
         this.$router.push({path:page,query: {id: this.$route.query.id,name:this.$route.query.name}});
        }
      },
      // 页面初始化
      created(){
       // console.log(this.$route.query.id);
      }
    }
</script>
<style scoped>

  .homeListTabOne{
    padding: 10px 20px;
  }
  .head {
    width: 5.3rem;
    margin: 0 auto;
    text-align: center;
    position: relative;
    padding-top: 3px;
    margin-bottom: 0;
  }
  .head img{
    width: 100%;
    margin-bottom: -.25rem;
    border: 1px solid white;
    border-radius: 50%;
  }
  .username{
    font-size: 1.3rem;
    color: black;
    margin-right: 10px;
  }
  .homeService{
    width: 48%;
    margin-right: 3%;
    background-color: white;
    border-radius: 5px;
    height: 6rem;
    float: left;
    position: relative;
    text-align: center;
    margin-bottom: .73rem;
  }
  .homeServiceCenterImg{
    width: 2.3rem;
    margin: 1.7rem 3% 0 0;
  }
  .homeServiceCenterZi{
    vertical-align: top;
    font-size: .9rem;
    margin-top: 2.35rem;
    display: inline-block;
  }
  .homeServiceRight{
    position: absolute;
    right: 0;
    top: -5px;
    background: url(../img/healthPercent.png) no-repeat;
    background-size: 100% 100%;
    /* background-position: 100% 91%; */
    color: white;
    width: 25%;
    height: 1.7rem;
    line-height: 1.7rem;
    text-align: center;
    margin: 0;
  }
  .percent{
    font-size: 1.1rem;
  }
  .sexImgSrc{
    width: 1.3rem;
    vertical-align: top;
    margin-top: .35rem;
    margin-right: 1%;
  }
  .age {
    color: #555;
    vertical-align: text-bottom;
    display: inline-block;
    font-size: 1rem;
  }
</style>



