
import {post,get} from '../../axios/ajax'
const loginBaseUrl = '';

export const loginUrls = {
  login: loginBaseUrl + '/common/getAreaList.json',
  logout: loginBaseUrl + '/admin/login/submit'
}
export const List = {
  list1:loginBaseUrl + '/search'
}
/**
 * 登录请求
 * @param params
 */
export const requestLogin = params => {
  console.log(params);
  return get(loginUrls.login, params)
}  //param 等于接收的data
export const getUser = params =>{
  return post(loginBaseUrl.login,params)
}
/**
 * 登出请求
 * @param params
 */
export const goLogout = params => {
  return post(loginUrls.logout, params)
}
/**
 * 列表请求
 * @param params
 */
export const getList = params => {
  console.log(params);
  return get(List.list1, params)
}  //param 等于接收的data



