<template>
  <div class="addPatient">
    <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
    <div class="hos_child">
        <div class="hos_list_top clearfix" v-for="(i,index) in bigList" style="border: none;height: auto;">
          <div class="left" style="    line-height: 1.3rem;margin-top: .8rem;">{{i.remark}}</div>
          <div class="right">
             <span v-for="(item,indexJ) in i.children" class="yibaoPay"  :class="{yibaoPayActive:item.bool}" @click="chooseMany(item,indexJ)">
              {{item.remark}}
            </span>
          </div>
        </div>

    </div>
    <button class="bindBtn" @click="postUserBodyInfo">{{sureBtn}}</button>
  </div>
</template>
<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
  import topheader from "../healthPageHome/topheader";
  let other = '其他';
  export default {
      name:'patientDetailLife',
      data: function () {
        return {
          topName:'个人体征数据',
          backHrefValZhi:'/patientDetail',
          lifeFunc:'',
          bigList:[],
          sureBtn:'确认',
          isFirstEnter:false // 是否第一次进入，默认false
        }
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data,query:{id:this.$route.query.id,name:this.$route.query.name}});
        },
        chooseMany(item,i){
          item.bool = !item.bool;
        },
        getUserStaticBodyInfo(){
          let elThis = this;
          let data = '';
          get(this.SEVERADDR + '/health_supervisor/findBodyInfoTypeTree', {params:data}).then((res) => {
            if(res.success){
                res.data.forEach(function (value) {
                  value.children.forEach(function(valuChild){
                    valuChild.bool = false;
                  });
                  elThis.bigList.push(value);
                })
              console.log(elThis.bigList)
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getUserBodyInfo(){
          let elThis = this;
          let data = {};
          data.userId = this.$route.query.id;
          get(this.SEVERADDR + '/health_supervisor/findUserTraitInfoDetail', {params:data}).then((res) => {
            if(res.success){
              if(res.data.bodyInfoTypeList.length > 0){
                res.data.bodyInfoTypeList.forEach(function (value) {
                  elThis.bigList.forEach(function (valueBig) { //往现有的list去一个个比较
                    valueBig.children.forEach(function(valueChild){
                      if(valueChild.id == value){
                        valueChild.bool = true;
                      }
                    })
                  });
                })
              }
            }else{
              if(res.data != '无法获取此用户个人基本信息'){
                elThis.$toast.error(res.data);
              }
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        postUserBodyInfo() {
          let elThis = this;
          let data = {};
          data['user.id'] = this.$route.query.id;
          let allChoose = [];
          this.bigList.forEach((value) => {
            value.children.forEach((valueChild)=>{
              if(valueChild.bool == true){
                allChoose.push(valueChild.id);
              }
            });
          });
          data.typeList = allChoose;
          console.log(data)
          post(this.SEVERADDR + '/health_supervisor/addUserTraitInfo', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            this.$toast.success('个人体征数据完善成功');
            if(this.$route.query.write == true || this.$route.query.write == 'true'){
              this.$router.push({
                path: '/patientDetail',
                query: {id:this.$route.query.id,name:this.$route.query.name,write:true}
              })
            }
            //window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'patientDetail'){
        to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        to.meta.isBack = true;//返回的
      }
      next();
    },
    activated(){
      if(this.$route.query.write == true || this.$route.query.write == 'true'){
        this.sureBtn = '下一步';
      }else{
        this.sureBtn = '确认';
      }
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        this.bigList = [];
        this.getUserStaticBodyInfo();
        this.getUserBodyInfo();
      }
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter=false;
    },
      // 页面初始化
      created(){
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      },
      components:{
        topheader
      }
    }
</script>
<style scoped>
  .addPatient{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #ebebeb;
  }
  .hos_child {
    margin-top: 53px;
    float: left;
    width: 100%;
    margin-left: 0;
    border-radius: 0;
    padding: 0 .2rem;
    padding-bottom: 0.2em;
    background-color: #ffffff;
  }
  .hos_child .hos_list_top {
    height: 2.9rem;
    /* border: 1px solid red; */
    font-size: 1rem;
    line-height: 2.5rem;
    /* display: flex; */
    float: left;
    width: 95%;
    color: #333333;
    margin-left: 2.8%;
    /* border-bottom: 1px solid #e5e5e5; */
  }
  .hos_list_top .left {
    width: 26%;
    margin: -.2rem 0 0 -0.5%;
    line-height: 3.3rem;
    padding-left: 3%;
    color: #555;
    float: left;
    font-size: .92rem;
  }
  .hos_list_top .right {
    padding-left: .2rem;
    margin-top: .2em;
    width: 70%;
    float: left;
  }
  .right input[type="text"] {
    width: 100%;
    height: 1.9rem;
    line-height: 2rem;
    border: 1px solid #bfbfbf;
    padding-left: 2%;
    outline: none;
    font-size: .85rem;
    border-radius: 3px;
  }
  .right select {
    width: 100%;
    height: 1.9rem;
    line-height: 1.7rem;
    border: 1px solid #a2a2a2;
    color: #5a5a5a;
    border-radius: 4px;
  }
  .sex:checked {
    background: url(../img/raido.png);
    background-size: 100% 100%;
    color: white;
    border: 0px;
  }
  .sex {
    float: left;
    width: 15px;
    margin-left: 12px;
    height: 15px;
    margin-top: .8rem;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background: #fff;
    border-radius: 50%;
    border: 1px solid #c0c0c0;
    color: #fff;
    display: block;
  }
  .sexname {
    float: left;
    margin: 0 2em 0 5px;
  }
  .yibaoPay{
    padding: 0 .4rem;
    height: 1.9rem;
    line-height: 1.9rem;
    border: 1px solid #c5c5c5;
    color: #555;
    font-size: .85rem;
    border-radius: 3px;
    display: inline-block;
    margin-right: .6rem;
  }
  .yibaoPayActive{
    background-color: #00afa1;
    color: white;
    border: 1px solid #00afa1;
  }
  .bindBtn {
    width: 52%;
    margin-left: 24%;
    border-radius: 5px;
    height: 2.5rem;
    line-height: 2.5rem;
    color: white;
    font-size: 1rem;
    margin-top: 6%;
    text-align: center;
    /* position: fixed; */
    bottom: 0;
    background-color: #00afa1;
    margin-bottom: 2rem;
    border: none;
  }
  .yibaoPayActive{
    background-color: #00afa1;
    color: white;
    border: 1px solid #00afa1;
  }
</style>



