<template>
  <div class="homeDiv">
    <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
    <button class="rulu" style="background-color:#00afa1;color: white;margin-top: 57px">录入</button>
    <div class="hos_child">
      <div class="ceTop">
        <div style="float: left;width: 33.3%;text-align: center">
          <div>高压</div>
          <div style="color: #777">mmHg</div>
        </div>
        <div style="float: left;width: 33.3%;text-align: center">
          <div>低压</div>
          <div style="color: #777;font-size: .8rem;">mmHg</div>
        </div>
        <div style="float: left;width: 33.3%;text-align: center">
          <div>心率</div>
          <div style="color: #777;font-size: .8rem;">bpm</div>
        </div>
      </div>
     <!-- <div class="clear"></div>-->
      <div class="hos_list_top clearfix">
        <div class="right" style="margin-left: -1%"><input class="xuezhiVal" v-model="maximumPressure" placeholder="" type="text"></div>
        <div class="right"><input class="xuezhiVal" placeholder="" v-model="minimumPressure" type="text"></div>
        <div class="right"><input class="xuezhiVal" placeholder="" v-model="heartRate" type="text"></div>
      </div>
    </div>
    <div class="clear"></div>
    <div class="ceTimeDiv">测量时间<span class="nowDay">{{chooseDaytime}}</span><div class="xuezhiVal ceTime" style="height: 1.8rem; cursor: pointer;" id="trigger3" @click="openBotttomSheet">{{choosetime}}</div></div>
    <button class="rulu post" @click="addXueya()">提交</button>
    <button class="rulu" style="margin-bottom: .4rem" onclick="">历史</button>
    <mu-container ref="container" class="demo-loadmore-content">
      <mu-bottom-sheet :open.sync="open">
        <div class="btnBar"><div class="fixWidth"><div class="cancel" @click="closeBottomSheet">取消</div><div class="title">选择时间</div><div class="ensure" @click="sureBottomSheet">确认</div></div></div>
        <mu-flex justify-content="center" align-items="start" wrap="wrap">
          <mu-paper :z-depth="1">
            <mu-time-picker color="tealA700" :no-display="!display" :view-type="viewType" :time.sync="time" :format="type" :landscape="landscape"></mu-time-picker>
          </mu-paper>
        </mu-flex>
      </mu-bottom-sheet>
      <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
        <div id="mescroll3" class="mescroll">
          <div v-for="i,index in list" class="hos_list_top clearfix">
            <div class="yuyueTop">
              <span class="famousName hosName">血压</span>
            </div>
            <div class="orderContent">
              <div class="left"><img src="../img/xueya.png"></div>
              <div class="rightList">
                <p class="detail">
                  <span class="name">高压 {{i.maximumPressure}}mmHg</span>
                </p>
                <p class="detail">
                  <span class="name">低压 {{i.minimumPressure}}mmHg</span>
                </p>
                <p class="detail" style="margin-bottom: .45rem;">
                  <span class="name">心率 {{i.heartRate}}mmHg</span>
                </p>
                <p class="orderdetailchoose" style="    margin-bottom: .5rem;">
                  <span>测量时间：</span>
                  <span class="orderdetailchooseName">{{i.monitoringDatetime}}</span>
                </p>
              </div>
            </div>
            <div class="clear"></div>
          </div>
        </div>
       <!-- <mu-list textline="three-line">
          <template v-for="i,index in list">
           &lt;!&ndash; <mu-list-item avatar button :ripple="false" style="    background-color: white;">
              <mu-list-item-action>
                <mu-avatar color="yellow600">
                  <mu-icon :value="insert_chart"></mu-icon>
                </mu-avatar>
              </mu-list-item-action>
              <mu-list-item-content>
                <mu-list-item-title>{{i.name}}</mu-list-item-title>
                <mu-list-item-sub-title style="color: rgba(0, 0, 0, .87)">{{i.hospital}}</mu-list-item-sub-title>
                <mu-list-item-sub-title>
                  {{i.diseaseLabel}}
                </mu-list-item-sub-title>
              </mu-list-item-content>
              <mu-list-item-action >
                <mu-icon class="toggle-icon" size="24" @click="caozuoWarn(index)" :color="index % 2 == 0 ? '' : 'yellow700'" :value="index % 2 == 0 ? 'alarm_add' : 'alarm_on'"></mu-icon>
             &lt;!&ndash;   <mu-checkbox color="yellow700" v-model="selects" value="value5"  uncheck-icon="alarm_add" checked-icon="alarm_on"></mu-checkbox>&ndash;&gt;
                <mu-list-item-after-text><mu-button small color="primary" @click="patientDetail(i.id,i.name)">详细档案</mu-button></mu-list-item-after-text>
              </mu-list-item-action>
            </mu-list-item>
            <mu-divider />&ndash;&gt;

          </template>
        </mu-list>-->
      </mu-load-more>
    </mu-container>
  </div>

</template>
<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError,formatDate} from "../assets/js/assist";
  import topheader from "../healthPageHome/topheader";
  export default {
      name:'patientDetailCheckXueya',
      data: function () {
        return {
          topName:'血压',
          backHrefValZhi:'/patientDetail',
          maximumPressure:'',
          minimumPressure:'',
          heartRate:'',
          monitoringDatetime:'',
          open: false,
          time: undefined,
          landscape: false,
          display: false,
          type: '24hr',
          viewType: 'list',
          chooseDaytime:'',
          choosetime:'',
          list:[],
          refreshing: false,
          loading: false,
          insert_chart:'insert_chart',
          selects:true,
          searchVal:'',
          pageNumber:1,
          isFirstEnter:false // 是否第一次进入，默认false
        }
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data,query: {id: this.$route.query.id,name:this.$route.query.name}});
        },
        addXueya () {
          let elThis = this;
          this.monitoringDatetime = this.chooseDaytime +' '+ this.choosetime + ':00';
          let data = {};
          data.userId = this.$route.query.id;
          data.maximumPressure = this.maximumPressure;
          data.minimumPressure = this.minimumPressure;
          data.heartRate = this.heartRate;
          data.monitoringDatetime = this.monitoringDatetime;
          console.log(data)
          post(this.SEVERADDR + '/health_supervisor/bloodPressure/addBloodPressure', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            if(res.success == true){
              this.$toast.success('添加成功');
              elThis.maximumPressure = '';
              elThis.minimumPressure = '';
              elThis.heartRate = '';
              elThis.pageNumber = 1;
              elThis.list = [];
              elThis.getList(elThis.pageNumber);
            }else{
              this.$toast.error(res.data);
            }
            //this.$router.go(0);
            //elThis.loading1 = !elThis.loading1;
           // window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getList(pageNumber){
          let elThis = this;
          let data = {};
          data.pageNumber = pageNumber;
          data.pageSize = 5;
          data.userId = this.$route.query.id;
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/bloodPressure/findPageByHealthSupervisorId', {params:data}).then((res) => {
            console.log(res);
            if(res.data.content.length > 0){
              res.data.content.forEach(function (value) {
                elThis.list.push(value);
              })
            }
            elThis.refreshing = false;
            elThis.loading = false;
          }).catch(function(err){
            ajaxError(err);
          })
        },
        refresh () {   //刷新，重新清空数组
          this.refreshing = true;
          this.pageNumber = 1;
          this.list = [];
          this.getList(this.pageNumber);
          this.$refs.container.scrollTop = 0;
        },
        load () {  //继续加载，push
          this.loading = true;
          this.pageNumber++;
          this.getList(this.pageNumber);
        },
        caozuoWarn(index){
          console.log(index)
          if(index % 2 == 0){

          }
        },
        patientDetail(id,name){
          this.$router.push({
            path: '/patientDetail',
            query: {id: id,name:name}
          })
        },
        sureBottomSheet(){
          this.open = false;
          if(this.time != undefined){
            this.choosetime = formatDate(this.time,'HH:mm');
          }
        },
        closeBottomSheet () {
          this.open = false;
        },
        openBotttomSheet () {
          this.open = true;
        }
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'patientDetail'){
        to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        to.meta.isBack = true;//返回的
      }
      next();
    },
    activated(){
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        this.choosetime = formatDate(new Date(),'HH:mm');
        this.chooseDaytime = formatDate(new Date(),'yyyy-MM-dd');
        this.list = [];
        this.pageNumber = 1;
        this.getList(this.pageNumber);
      }
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter=false;
    },
      // 页面初始化
      created(){
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      },
      components:{
        topheader
      }
    }
</script>
<style scoped>
  .clear{
    clear: both;
  }
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #f1f1f1;
  }
  .rulu {
    border-radius: 4px;
    font-weight: 500;
    font-size: .8rem;
    margin: 1rem 3% 1rem 3%;
    background-color: rgba(255,255,255,0);
    border: 1px solid #00afa1;
    color: #00afa1;
    outline: none;
    padding: .25rem 7.5%;
  }
  .hos_child {
    /* height: 8rem; */

    float: left;
    background-color: white;
    width: 94%;
    margin-left: 3%;
    margin-top: -.1rem;
    padding: .4rem 0;
  }
  .ceTop {
    width: 100%;
    /* height: 1rem; */
    float: left;
    border-bottom: 1px solid #dddddd;
    padding-bottom: .2rem;
  }
  .hos_child .hos_list_top {
    /* height: 2.9rem; */
    /* border: 1px solid red; */
    font-size: .9rem;
    /* line-height: 2.5rem; */
    /* display: flex; */
    /* float: left; */
    width: 100%;
    color: #333333;
    /* padding-left: 2.8%; */
    /* border-bottom: 1px solid #e5e5e5; */
  }
  .hos_list_top .right {
    padding-left: 6%;
    /* margin-top: -2rem; */

    float: left;
    width: 33.3%;height: 3rem;line-height: 3.3rem;margin-left: 0;
  }
  .right input[type="text"] {
    width: 85%;
    height: 1.8rem;
    line-height: 1.8rem;
    outline: none;
    font-size: .85rem;
    text-align: center;
  }
  .xuezhiVal {
    border: 1px solid #dddddd;
    border-radius: 3px;
  }
  .post {
    width: 60%;
    margin-left: 20%;
    background-color: #00afa1;
    padding: .4rem 7%;
    color: white;
    font-size: 1rem;
    margin-top: 0rem;
    margin-bottom: .7rem;
  }
  .ceTimeDiv {
    font-size: .8rem;
    line-height: 3rem;
    margin-top: .5rem;
    background-color: white;
    width: 94%;
    margin-left: 3%;
    padding-left: 4%;
    margin-bottom: 1.3rem;
  }
  .nowDay {
    font-size: .8rem;
    display: inline-block;
    margin-left: 3%;
  }
  .ceTime {
    height: 2rem;
    line-height: 1.7rem;
    outline: none;
    font-size: .85rem;
    width: 22%;
    margin-left: 4%;
    text-align: center;
    display: inline-block;
    margin-top: .6rem;
    vertical-align: top;
  }
  .mobileSelect .content .btnBar {
    border-bottom: 1px solid #DCDCDC;
    font-size: 15px;
    height: 45px;
    position: relative;
    text-align: center;
    line-height: 45px;
  }
  .fixWidth {
    width: 90%;
    margin: 0 auto;
    position: relative;
  }
  .btnBar {
    border-bottom: 1px solid #DCDCDC;
    font-size: 15px;
    height: 45px;
    position: relative;
    text-align: center;
    line-height: 45px;
  }
.btnBar .cancel {
    left: 0;
    color: #666;
  }
.btnBar .cancel,.btnBar .ensure{
    height: 45px;
    width: 55px;
    cursor: pointer;
    position: absolute;
    top: 0;
  }
.btnBar .title {
    font-size: 15px;
    padding: 0 15%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  color: black;
  }
.btnBar .ensure {
    right: 0;
    color: #00afa1;
  }
  .hos_list_top {
    font-size: .9rem;
    width: 100%;
    color: #333333;
    background: white;
    margin-top: .9rem;
  }
  .yuyueTop {
    width: 100%;
    color: black;
    font-size: .9rem;
    margin: .1rem 0 0 0%;
    height: 2.2rem;
    line-height: 2rem;
    clear: both;
    border-bottom: solid 1px #dcdcdc;
  }
  .famousName {
    display: inline-block;
    font-size: .92rem;
    margin-left: 4%;
  }
  .orderContent {
    width: 96%;
    color: black;
    font-size: .2rem;
    margin: .7rem 0 0 4%;
    height: 5.5rem;
    clear: both;
    /* border-bottom: solid 1px #dcdcdc; */
  }
  .hos_list_top .left {
    width: 19%;
    margin: .1rem 0 0 -0.5%;
    line-height: 3.3rem;
    padding-left: 0;
    color: #222;
    float: left;
  }
  .hos_list_top .left img {
    max-height: 4.3rem;
    overflow: hidden;
    width: initial;
    padding: .2rem;
  }
  .hos_list_top .rightList {
    padding-left: 6%;
    margin-top: -.5rem;
    width: 75%;
    float: left;
  }
  .rightList p {
    margin-bottom: .2rem;
    height: 1.4rem;
    line-height: 1.4rem;
    margin-top: .3rem;
    font-size: .85rem;
  }
</style>



