<template>
  <div id="app">
  <!--  <img src="./assets/logo.png">-->
    <router-link to="home"></router-link>

   <!-- <router-view/>-->
    <main>
      <!-- 右侧主内容区 -->
      <div  class="main-right" >
        <!--<router-view/>-->
        <transition name="fade">
          <!--<keep-alive>
            <router-view/>
          </keep-alive>-->
          <keep-alive>
            <router-view v-if="$route.meta.keepAlive"/>
          </keep-alive>
          <router-view v-if="!$route.meta.keepAlive"/>
        </transition>
      </div>
    </main>


  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      active:true,
      isCollapse:false
    }
  },
  created(){

  },
  watch:{
    '$route'(to,from){
      if(to.path == '/home'){
          this.active = true;
      }else if(to.path == '/activeList'){
        this.active = false ;
      }
    }
  },
  methods:{
    handleOpen(){
      console.log(555)
    }
  }
}
</script>

<style>
#app {
  height: 100%;
  width: 100%;
}
/* 头部导航 */
/* 路由切换动效 */
.fade-enter-active, .fade-leave-active {
  transition: all .5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-active {
  opacity: 0;
  transform: translateY(30px);
}
/* 主内容区 */
main{    display: -webkit-box;  display: -ms-flexbox;  display: flex;}
main .main-left{text-align: center;width: 200px;float: left;}
main .main-right{-webkit-box-flex: 1;  -ms-flex: 1;  flex: 1;  background-color: white; padding: 50px 0; }
main .el-menu{background-color: transparent!important;}
  .logo{
    float: left;margin-right: 50px
  }
</style>



