<template>
  <div class="homeDiv">
   <!-- <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>-->
 <!--   <img class="addMescroll1Img" @click="addJiuzhenBingli" src="../img/add.png">-->
    <mu-container ref="container" class="demo-loadmore-content">
    <!--  <mu-bottom-sheet :open.sync="open">
        <div class="btnBar"><div class="fixWidth"><div class="cancel" @click="closeBottomSheet">取消</div><div class="title">选择时间</div><div class="ensure" @click="sureBottomSheet">确认</div></div></div>
        <mu-flex justify-content="center" align-items="start" wrap="wrap">
          <mu-paper :z-depth="1">
            <mu-time-picker color="tealA700" :no-display="!display" :view-type="viewType" :time.sync="time" :format="type" :landscape="landscape"></mu-time-picker>
          </mu-paper>
        </mu-flex>
      </mu-bottom-sheet>-->
      <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
        <div id="mescroll3" class="mescroll">
          <div class="hos_child caseDiv set_box_shadow" v-for="i,index in list">
            <div class="hos_list_top clearfix">
              <div class="orderContent">
                <div class="left" style=""><img :src="i.fullHeadImgUrl"></div>
                <div class="right"><p class="detail"><span class="name" style="font-size: 1.05rem">{{i.userName}}</span>
                  <a :href="'tel:' + i.mobile"><span class="orderdetailchooseName">{{i.mobile}}</span></a>
                </p>
                  <p class="orderdetailchoose" style="color: #999"><span>需回访时间：</span> <span
                    class="orderdetailchooseName">{{i.lastGreetDate | FormatData}}</span>
                    <button class="orderReadMore"  @click="toAccess(i.hsGreetId,i.reservationId)">去回访</button>
                  </p>
                  <p class="detail" style="line-height: 1rem;color: #999;    height: auto;"><span
                    class="name overflow_num_ellipsis">上门地址：{{i.title}}</span></p>
                  <p class="detail" style="line-height: 1rem;color: #999;    height: auto;"><span class="name overflow_num_ellipsis">预约事项：{{i.lastCurRemark}}</span>
                    <button class="orderReadMore" style="background-color: #00afa1;padding: .25rem .4rem;margin-bottom: .8rem;"  @click="toAccessHistory(i.hsGreetId,i.reservationId)">历史记录</button>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </mu-load-more>
    </mu-container>
  </div>

</template>
<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError,formatDate} from "../assets/js/assist";
  export default {
      name:'patientDetailCheckXueya',
      data: function () {
        return {
          maximumPressure:'',
          minimumPressure:'',
          heartRate:'',
          monitoringDatetime:'',
          time: undefined,
          landscape: false,
          display: false,
          type: '24hr',
          viewType: 'list',
          list:[],
          refreshing: false,
          loading: false,
          insert_chart:'insert_chart',
          selects:true,
          searchVal:'',
          pageNumber:1
        }
      },
      methods: {
        getList(pageNumber){
          let elThis = this;
          let data = {};
          data.pageNumber = pageNumber;
          data.pageSize = 10;
          data.needGreet = true;
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/greet/needGreet', {params:data}).then((res) => {
            console.log(res);
            if(res.data.length > 0){
              res.data.forEach(function (value) {
                elThis.list.push(value);
              })
            }
            elThis.refreshing = false;
            elThis.loading = false;
          }).catch(function(err){
            ajaxError(err);
          })
        },
        refresh () {   //刷新，重新清空数组
          this.refreshing = true;
          this.pageNumber = 1;
          this.list = [];
          this.getList(this.pageNumber);
          this.$refs.container.scrollTop = 0;
        },
        load () {  //继续加载，push
          this.loading = true;
          this.pageNumber++;
          this.getList(this.pageNumber);
        },
        caozuoWarn(index){
          console.log(index)
          if(index % 2 == 0){

          }
        },
        patientDetail(id,name){
          this.$router.push({
            path: '/patientDetail',
            query: {id: id,name:name}
          })
        },
        addJiuzhenBingli(){
          this.$router.push({
            path: '/postPhotos',
            query: {id: this.$route.query.id,name:this.$route.query.name}
          })
        },
        toAccess(id,reservationId){
          let ID = id;
          let ReservationId = reservationId;
          this.$router.push({
            path: '/accessUserAdd',
            query: {id:ID,reservationId:ReservationId}
          })
        },
        toAccessHistory(id,reservationId){
          let ID = id;
          let ReservationId = reservationId;
          this.$router.push({
            path: '/accessUserOneHistory',
            query: {id:ID,reservationId:ReservationId}
          })
        }
      },
    activated(){
      console.log('子组件进来');
      this.pageNumber = 1;
      this.list = [];
      this.getList(this.pageNumber);
      //if(!this.$route.meta.isBack  || this.isFirstEnter){
        //this.choosetime = formatDate(new Date(), 'HH:mm');
        //this.chooseDaytime = formatDate(new Date(), 'yyyy-MM-dd');
        //this.list = [];
        //this.getList(this.pageNumber);
      //}
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      //this.$route.meta.isBack = false;
      //this.isFirstEnter=false;
    },
      // 页面初始化
      created() {

      }
    }
</script>
<style scoped>
  .clear{
    clear: both;
  }
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #f1f1f1;
  }
  .hos_child {
    /* height: 8rem; */
    padding-top: .2rem;
    padding-bottom: 0.2em;
    margin-top: .9rem;
    float: left;
    background-color: white;
    width: 100%;
  }
  .set_box_shadow {
    width: 98%;
    position: relative;
    /* margin: 0 auto; */
    margin-left: 1%;
    margin-top: .9rem;
    border-radius: 5px;
    background-color: #ffffff;
    /* box-shadow: 0px 6px 20px 0 rgba(127, 127, 127, 0.19); */
    font-family: PingFang-SC;
    padding: 0 .2rem;
  }
  .hos_child .hos_list_top {
    /* height: 2.9rem; */
    /* border: 1px solid red; */
    font-size: .9rem;
    /* line-height: 2.5rem; */
    /* display: flex; */
    /* float: left; */
    width: 100%;
    color: #333333;
    /* padding-left: 2.8%; */
    /* border-bottom: 1px solid #e5e5e5; */
  }
  .orderContent {
    width: 96%;
    color: black;
    font-size: .85rem;
    height: 5.5rem;
    clear: both;
    margin: .5rem 0 0 2%;
    /* border-bottom: solid 1px #dcdcdc; */
  }
  .hos_list_top .left {
    width: 24%;
    line-height: inherit;
    margin: .1rem 0 0 -0.5%;
    padding-left: 0;
    color: #222;
    float: left;
  }
  .hos_list_top .left img {
    max-height: 4.9rem;
    overflow: hidden;
    width: initial;
    padding: .2rem;
    border-radius: 50%;
  }
  .hos_list_top .right {
    width: 75%;
    float: left;
    margin-top: .1rem;
    padding-left: 2%
  }
  .right p {
    margin-bottom: .3rem;
    height: 1.4rem;
    margin-top: 0;
    line-height: 1.4rem;
  }
  .name, .sexImg {
    /* margin-right: 10px; */
  }
  .overflow_num_ellipsis {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  .orderReadMore {
    border: none;
    border-radius: 3px;
    color: white;
    font-weight: 500;
    font-size: .9rem;
    float: right;
    margin-left: 1.5%;
    background-color: rgba(22, 155, 213, 1);
    outline: none;
    padding: .1rem .3rem;
  }
  .addMescroll1Img {
    width: 2.5rem;
    position: fixed;
    bottom: 0.8rem;
    right: 3%;
    z-index: 9;
  }
</style>



