<template>
  <div class="homeDiv"  style="background-color: #f0f0f0">
    <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
    <mu-container style="padding: 0">
      <div id="chatArea" class="box chat">
        <div class="box_hd"></div>
        <div class="box_bd" id="messageList" ref="messageList" style="margin-bottom: 3rem;height: 100%;">
          <div class="messageIfDiv" v-for="(i,index) in list">
          <div id="messageDiv" class="message me" v-if="i.roleName == '健管师'"  ordersn="1"><img class="avatar" src="../img/myLogo.png">
            <p class="sendTime">{{i.sendDatetime | FormatDataTime}}</p>
            <div class="chatcontent">
              <div class="bubble bubble_primary right">
                <div class="bubble_cont">
                  <div class="plain">
                    <pre>{{i.msg}}</pre>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="message" v-else><img class="avatar" src="../img/hosLogo.png">
            <p class="getTime">{{i.sendDatetime | FormatDataTime}}</p>
            <div class="chatcontent">
              <div class="bubble bubble_default left">
                <div class="bubble_cont">
                  <div class="plain">
                    <pre>{{i.msg}}</pre>
                  </div>
                </div>
              </div>
            </div>
          </div>
            <div style="clear: both"></div>
          </div>
          <div style="clear: both"></div>
        </div>
      </div>
      <div class="box_ft">
        <div class="box_ft_hd">
          <div class="eaitWrap">
            <!--<pre id="editArea" class="editArea" contenteditable="true"></pre>-->
            <input type="text" id="editArea" v-model="editArea" class="editArea">
          </div>
          <a href="javascript:;" style="display: inline-block" class="btn btn_send" id="btn_send" @click="addNews">发送</a>
        </div>
      </div>
    </mu-container>
    </div>


</template>
<script>
  import {get,post} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
  // 地址选择器组件
  import topheader from "../healthPageHome/topheader";
  // import store from '../assets/js/store.js'
  export default {
      name:'chat',
      // 组件
      components: {
        topheader
      },
      data: function () {
        return {
          backHrefValZhi:'/newsCenter',
          topName:this.$route.query.name,
          editArea:'',
          intime:null,
          list:[],
          offsetHeight:'',
          isFirstEnter:false // 是否第一次进入，默认false
        }
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data});
        },
        getChatHistory(){
          let elThis = this;
          let data = {};
          data.chatMainBodyId = this.$route.query.id;
          data.pageNumber = 1;
          data.pageSize = 50;
          get(this.SEVERADDR + '/chat/main/body/chatHistory', {params:data}).then((res) => {
            if(res.success){
              if(res.data.length > 0){
                res.data.forEach(function (value) {
                  elThis.list.unshift(value);
                });
              }
              elThis.offsetHeight= this.$refs.messageList.offsetHeight + 100;
              setTimeout(function () {
                window.scrollTo(0,elThis.offsetHeight);
              },100);
              elThis.getPullNew();
              clearInterval(elThis.intime);
              elThis.intime = setInterval(function () {
                elThis.getPullNew();
              },1500);
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getPullNew(){
          let elThis = this;
          let data = {};
          data.chatMainBodyId = elThis.list[elThis.list.length-1].chatMainBodyId || this.$route.query.id;
          data.orderSn = elThis.list[elThis.list.length-1].orderSn;
          get(this.SEVERADDR + '/health_supervisor/chat/main/body/chatHistory/pullNew', {params:data}).then((res) => {
            if(res.success){
              if(res.data.length > 0){
                res.data.forEach(function (value) {
                  elThis.list.push(value);
                })
              }
              elThis.offsetHeight= this.$refs.messageList.offsetHeight + 100;
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        addNews () {
          if(this.editArea == ''){
            this.$toast.info('请输入消息');
            return
          }
          let elThis = this;
          let data = {};
          data.chatMainBodyId = elThis.list[elThis.list.length-1].chatMainBodyId || this.$route.query.id;
          data.version = elThis.list[elThis.list.length-1].chatMainBodyVersion || this.$route.query.veision;
          data.msg = this.editArea;
          post(this.SEVERADDR + '/health_supervisor/hs_chat_msg/commit', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            if(res.success == true){
                elThis.getPullNew();
                this.editArea = '';
                setTimeout(function () {
                  window.scrollTo(0,elThis.offsetHeight);
                },100);
            }else{
              this.$toast.error(res.data);
            }
            //this.$router.go(0);
            //elThis.loading1 = !elThis.loading1;
            // window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'newsCenter'){
        to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        to.meta.isBack = true;//返回的
      }
      next();
    },
    beforeRouteLeave(to, from, next) {
      console.log(this.intime)
      clearInterval(this.intime);　　// 清除定时器
      //this.intime = null;
      next();
    },
    activated(){
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        this.list = [];
        this.getChatHistory();
      }
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter=false;
    },
      // 页面初始化
      created(){
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      }
    }
</script>
<style>
