    position: absolute;
    top: 14px;
    border: 6px solid transparent;
    content: " ";
  }

  .bubble.left:before, .bubble.left:after {
    right: 100%;
  }

  .bubble.left:after {
    border-right-color: #FFF;
    border-right-width: 8px;
  }

  .bubble.right:before, .bubble.right:after {
    left: 99.75%;
  }

