<template>
  <div class="pagination">
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 30]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
  let pageNum = 1;
  export default {
    props: {
      total: {
        type: Number,
        default: 0
      },
      currentPage: {
        type: Number,
        default: 1
      }
    },
    data() {
      return {
        pageSize: 10
      };
    },
    methods: {
      handleSizeChange(val) {
        console.log('每页' + val + '条')
        this.pageSize = val;
        this.$emit('search', 1, this.pageSize);
      },
      handleCurrentChange(val) {
        console.log('当前第' + val + '页')
        pageNum = val;
        this.$emit('search', pageNum, this.pageSize);
      }
    }
  };
</script>

<style scoped>
  .el-table-self{
    margin-left:16px;
    margin-right:16px;
    height:80%;
  }
  .pagination-footer .description{
    float:left;
    margin-left:20px;
    margin-top:12px;
  }
  .pagination-footer .el-pagination{
    float:right;
    margin-top:8px;
    margin-bottom:8px;

  }

  .el-table__empty-block {
    position: relative;
    min-height: 60px;
    text-align: center;
    width: 100%;
    height: 100%;

  }
  .el-table td, .el-table th {
    height: 30px;
    min-width: 0;
    text-overflow: ellipsis;
    vertical-align: middle;
  }
  .el-table__empty-text {
    position: absolute;
    left: 50%;
    width: 110px;
    height: 110px;
    top: 50%;
    line-height:220px;
    -ms-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    color: #5e7382;
    /*background-image: url(../../resource/images/null-data.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size:cover;*/
  }
</style>



