        postUserPersonalHealthInfo() {
          let elThis = this;
          let data = {};
          data.userId = this.$route.query.id;
          data.height = this.heigth;
          data.weight = this.weight;
          data.waist = this.yaowei;
          data.hip = this.tunwei;
          //------血型
          this.xuexingList.forEach((value) => {
            if(value.bool == true){
              data.bloodType = value.name;
            }
          });
          //------不适
            if(this.bushi == true && this.bushiVal != '') {
              data.discomfort = this.bushiVal;
            }
          //------患病
          if(this.huanbing == true) {
            let huanbingInputList = [];
            this.huanbingInputList.forEach((value) => {
              if(value.value != ''){
                huanbingInputList.push(value.value);
              }
            });
            data.ill = huanbingInputList.join('|');
          }
          //------家庭健康
          let familyList = [];
          if(this.huanbingFatherListShow == true) {
            let familyFatherObj= {};
            let familyFatherList= [];
            this.huanbingFatherList.forEach((value) => {
              if(value.bool == true){
                familyFatherList.push(value.name);
              }
            });
            if(this.huanbingFatherInputShow == true) {
              this.huanbingFatherInputList.forEach((value) => {
                if(value.value != ''){
                  familyFatherList.push(value.value);
                }
              });
            }
            familyFatherObj.father = familyFatherList.join('|');
            familyList.push(familyFatherObj);
          }
          if(this.huanbingMotherListShow == true) {
            let familyMotherObj= {};
            let familyMotherList= [];
            this.huanbingMotherList.forEach((value) => {
              if(value.bool == true){
                familyMotherList.push(value.name);
              }
            });
            if(this.huanbingMotherInputShow == true) {
              this.huanbingMotherInputList.forEach((value) => {
                if(value.value != ''){
                  familyMotherList.push(value.value);
                }
              });
            }
            familyMotherObj.mother = familyMotherList.join('|');
            familyList.push(familyMotherObj);
          }
          if(this.huanbingBrosisListShow == true) {
            let familyBrosisObj= {};
            let familyBrosisList= [];
            this.huanbingBrosisList.forEach((value) => {
              if(value.bool == true){
                familyBrosisList.push(value.name);
              }
            });
            if(this.huanbingBrosisInputShow == true) {
              this.huanbingBrosisInputList.forEach((value) => {
                if(value.value != ''){
                  familyBrosisList.push(value.value);
                }
              });
            }
            familyBrosisObj.brosis = familyBrosisList.join('|');
            familyList.push(familyBrosisObj);
          }
          if(this.huanbingChildrenListShow == true) {
            let familyChildrenObj= {};
            let familyChildrenList= [];
            this.huanbingChildrenList.forEach((value) => {
              if(value.bool == true){
                familyChildrenList.push(value.name);
              }
            });
            if(this.huanbingChildrenInputShow == true) {
              this.huanbingChildrenInputList.forEach((value) => {
                if(value.value != ''){
                  familyChildrenList.push(value.value);
                }
              });
            }
            familyChildrenObj.children = familyChildrenList.join('|');
            familyList.push(familyChildrenObj);
          }

          data.familyHealthSituation = JSON.stringify(familyList);
          //------既往史
          if(this.jiwangshi == true) {
            let jiwangshiInputList = [];
            this.jiwangshiInputList.forEach((value) => {
              if(value.value != ''){
                jiwangshiInputList.push(value.value);
              }
            });
            data.pastHis = jiwangshiInputList.join('|');
          }
          //------药物过敏
          if(this.yaowuguominListShow == true) {
            let yaowuguominList = [];
            this.yaowuguominList.forEach((value) => {
              if(value.bool == true){
                yaowuguominList.push(value.name);
              }
            });
            if(this.qitaYaowuguominInputShow == true) {
              this.qitaYaowuguominInputList.forEach((value) => {
                if(value.value != ''){
                  yaowuguominList.push(value.value);
                }
              });
            }
            data.drugAllergyHis = yaowuguominList.join('|');
          }
          //------食物接触物过敏
          if(this.guomin == true) {
            let guominInputList = [];
            this.guominInputList.forEach((value) => {
              if(value.value != ''){
                guominInputList.push(value.value);
              }
            });
            data.foodAndContactAllergyHis = guominInputList.join('|');
          }
          //------服药
          if(this.fuyao == true) {
            let fuyaoInputListList = [];
            this.fuyaoInputList.forEach((value) => {
              if(value.value != ''){
                fuyaoInputListList.push(value.value);
              }
            });
            data.takeMedicine = fuyaoInputListList.join('|');
          }
          //------疫苗
          if(this.yimiao == true) {
            let yimiaoInputListList = [];
            this.yimiaoInputList.forEach((value) => {
              if(value.value != ''){
                yimiaoInputListList.push(value.value);
              }
            });
            data.vaccinationHis = yimiaoInputListList.join('|');
          }
          console.log(data);
          post(this.SEVERADDR + '/health_supervisor/edit/userBasicHealthInfo', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            this.$toast.success('健康基础信息完善成功');
            if(this.$route.query.write == true || this.$route.query.write == 'true'){
              this.$router.push({
                path: '/patientDetailLife',
                query: {id:this.$route.query.id,name:this.$route.query.name,write:true}
              })
            }
            //window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'patientDetail'){
        to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        to.meta.isBack = true;//返回的
      }
      next();
    },
    activated(){
      if(this.$route.query.write == true || this.$route.query.write == 'true'){
        this.sureBtn = '下一步';
      }else{
        this.sureBtn = '确认';
      }
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        this.getUserPersonalHealthInfo();
      }
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter=false;
    },
      // 页面初始化
      created(){
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      },
      components:{
        topheader
      }
    }
