    border: 1px solid #a2a2a2;
    color: #5a5a5a;
    border-radius: 4px;
  }
  .sex:checked {
    background: url(../img/raido.png);
    background-size: 100% 100%;
    color: white;
    border: 0px;
  }
  .sex {
    float: left;
    width: 15px;
    margin-left: 12px;
    height: 15px;
    margin-top: .8rem;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background: #fff;
    border-radius: 50%;
    border: 1px solid #c0c0c0;
    color: #fff;
    display: block;
  }
  .sexname {
    float: left;
    margin: 0 2em 0 5px;
  }
  .yibaoPay{
    padding: 0 .4rem;
    height: 1.9rem;
    line-height: 1.9rem;
    border: 1px solid #c5c5c5;
    color: #555;
    font-size: .85rem;
    border-radius: 3px;
    display: inline-block;
    margin-right: .6rem;
  }
  .yibaoPayActive{
    background-color: #00afa1;
    color: white;
    border: 1px solid #00afa1;
  }
  .bindBtn {
    width: 52%;
    margin-left: 24%;
    border-radius: 5px;
    height: 2.5rem;
    line-height: 2.5rem;
    color: white;
    font-size: 1rem;
    margin-top: 6%;
    text-align: center;
    /* position: fixed; */
    bottom: 0;
    background-color: #00afa1;
    margin-bottom: 2rem;
    border: none;
  }
  .yibaoPayActive{
    background-color: #00afa1;
    color: white;
    border: 1px solid #00afa1;
  }
  .addInput {
    width: 1.5rem;
    margin: .55rem 0 0 1rem;
    vertical-align: top;
  }
</style>



