<template>
  <mu-appbar color="tealA700" style="height: 47px;position: fixed;top: 0;width: 100%;">
    <mu-button icon slot="left" @click="toBack()">
      <mu-icon value="arrow_back_ios"></mu-icon>
    </mu-button>
    {{headername}}
  </mu-appbar>
</template>

<script>
    export default {
        name: "topheader",
        props:["headername","backHrefVal"],
        methods:{
          toBack(){
            this.$emit('backHrefVal',this.backHrefVal)
          }
        }
    }
</script>

<style scoped>

</style>



