<template>
  <div>
    <button @click="btnzi('输出出场顺序')"><img src="../assets/logo.png"/><span>{{buttonZi}}</span></button>
    <span v-for="ins in btnziList">
      <input :value="ins.zi" type="text"/>
    </span>
  </div>

</template>

<script>
    export default {
      name: "child",
      data(){
        return{
          number:100
        }
      },
      mounted(){
        this.$nextTick(()=>{
          console.log('nextTick')
        });
          setTimeout(() => {
            this.btnziList[0] = '11111d';
            console.log(this.btnziList)
          }, 1000);

      },
      props: {  //接收值
        buttonZi: {
          type:String
        },
        btnziList:{
          type:Array
        }
      },
      methods:{
        btnzi(value){
          this.number = 1;
          this.$emit("getbtnzi",value);
        }
      },
      watch:{
        number(){  //number字段的监听
          if(this.number == 1){
            console.log('我是watch的1')
          }
        }
      }
    }
</script>

<style scoped>
  img{
    width: 20px;
  }
</style>



