<template>
  <div class="p-login p-middle">
    <span style="float: right">您还未登录，<span style="color: blue"  @click="toRouter">去登陆</span></span>
    <!-- element步骤组件 -->
    <el-steps :space="200" :active="step" class="step">
      <el-step title="活动信息" description=""></el-step>
      <el-step title="报名签到" description=""></el-step>
      <el-step title="分享设置" description=""></el-step>
    </el-steps>
    <router-view/>
    <div class="but-group">
      <el-button >预览</el-button>
      <el-button @click.native.prevent="handlePreStep" v-show="preStep">上一步</el-button>
      <el-button  type="primary" @click.native.prevent="handleNextStep" v-show="nextStep">下一步</el-button>
      <el-button  type="primary" @click.native.prevent="handlePublish" v-show="publish">发布活动</el-button>
    </div>
     <!-- @search是emit('search',11)接收11  :total是子页面prop接收total-->

  </div>

</template>


<script>
  import {requestLogin,goLogout,getList} from '../assets/js/index'
  import {post} from '../axios/ajax'
  import axios from 'axios'
  import add from "../components/add";
  import Child from "../components/child";
  import setlist from '../components/setlist'
  import pagination from '../components/page'
  import Swiper from "../components/swiper";
  import $ from 'jquery'

  export default {
    data () {
      return {
        username: '',
        password: '',
        id:5,
        resList:[],
        items:[{id:5,param:'csx'},{id:6,param:'csx1111'}],
        btnfont:'你好测试下',
        btnzilist:[{id:5,zi:'csx111'},{id:6,zi:'csx222'}],
        tableList:[],
        total:'',
        curPage:'',
        publish:false,
        preStep:false,
        nextStep:true,
        step:1
      }
    },
    created(){
      requestLogin({username: this.username,password: this.password}).then(res =>{
        console.log(res);
        this.resList = res.data;
        console.log(this.resList)
      });

    },
    mounted(){
      this.$nextTick(()=>{
        this.getData();
      })
    },
    methods: {
      handlePreStep(){
          this.$router.go(-1);
        let _this = this;


            _this.step--;
            _this.goStep(_this.step);


        $('html,body').animate({scrollTop:0},500);
      },
      handleNextStep(){
        console.log(this.step);
        this.$router.push('/distribute/step'+ (this.step+1));
        let _this = this;

            _this.step++;
            _this.goStep(_this.step);


        $('html,body').animate({scrollTop:0},500);
      },
      handlePublish(){
        console.log('发布')
      },
      goStep(step){
          switch (step){
            case 1:
              this.preStep = false;
              this.nextStep = true;
              this.publish = false;
                  break;
            case 2:
              this.preStep = true;
              this.nextStep = true;
              this.publish = false;
              break;
            case 3:
              this.preStep = true;
              this.nextStep = false;
              this.publish = true;
              break;
          }
      },
      toRouter(){
        this.$router.push({path: '/HelloWorld',query:{ id: this.id}});
      },
      goLogin(){

        /*requestLogout({}).then(res =>{
          console.log(res);
        }).catch(console.warn('错误'));
        requestLogin({username: this.username,password: this.password}).then(res =>{
          console.log(res);
        }).catch(console.warn('错误'));*/
        goLogout({username: 'uuuuuu',password: 'e882b72bccfc2ad578c27b0d9b472a14',loginFrom:101}).then(res =>{
          console.log(res);
          alert('登陆成功');
          this.username = res.data;
          //this.$router.push({path: '/HelloWorld',query:{ id: this.username}});
        });
       /*let data = {};
       data.username = 'admin';
       data.password = 'enuo120.'
        axios.post('/api/admin/login/submit',{data},)
          .then(function (response) {
            console.log(response);
          }).catch(function (response) {
            console.log(response);
          });*/
        //this.$router.push({path: '/HelloWorld',query:{ id:this.password}});
      },
      consolrBtn(data){
        console.log(data)
      },

      search(pageNum1,pageSize1){
        let pageNum = this.curPage = (typeof (pageNum1) === 'number') ? pageNum1 : 1;
        if (pageSize1) {
          this.pageSize = pageSize1;
        };
        let pageSize = pageSize1 || this.pageSize;
        this.getData(pageNum,pageSize);
      },
      getData(num,pageSize) {
        /*  getList({q:'休闲鞋',n:20,page:2}).then(res =>{
          console.log(res.totalPage);
          console.log(res.itemsArray);
          console.log(res.pageSize);
          this.tableList = res.itemsArray;
          this.total = Number(res.totalPage) * 5;
          this.curPage = res.page;
        });*/

        let self = this;
      self.curPage = num
        console.log(num)
        if (!num) {
          num = 1
        }
        if (!pageSize) {
          pageSize = 10
        }
        let data = {};//self.param(self.dataObj,num,pageSize);
        data.q = 20;
        data.page = num;
        //data.page = pageSize

        /*params.append('param',data);
        if(process.env.NODE_ENV === 'development'){
          self.url = '/s1/copUser/getUserList';
        };*/
      /*  getList(data).then(res => {
          console.log(res.totalPage);
          console.log(res.itemsArray);
          console.log(res.pageSize);
          self.tableList = res.itemsArray;
          self.total = Number(res.totalPage) * 5;
          self.curPage = res.page;
        });*/
      console.log(data)
        //axios.post()
        post('/api/search', data).then((res) => {
          console.log(res)
          self.tableList = res.itemsArray;
          self.total = Number(res.totalPage) * 5;
          //self.curPage = self.page;  其实要在这里写入返回的第几页
        })
      }
    },
    components:{
      Swiper,
      add,
      Child,
      setlist,
      pagination
    }
  }
</script>

<style>

</style>



