<template>
  <div class="homeDiv" style="background-color: #f4f4f4">
    <div class="homeList" v-loading="loading1">
      <div class="homeTop">
        <div class="head">
          <img src="../img/user_pic.png"><span class="name">{{healthname}}</span>
        </div>
      </div>
      <mu-button class="toHrefbtn" style="margin-bottom: 17px;" large full-width @click="toRouterPage('/addPatient')">添加患者 ></mu-button>
      <mu-button class="toHrefbtn" style="margin-bottom: 17px;" large full-width @click="toRouterPage('/patientList')">患者健康档案 ></mu-button>
      <!--<mu-button class="toHrefbtn" large full-width>定时提醒 ></mu-button>-->
      <mu-button class="toHrefbtn" style="margin-bottom: 17px;" large full-width @click="toRouterPage('/accessUser')">患者回访 ></mu-button>
      <mu-button class="toHrefbtn" style="margin-bottom: 17px;" large full-width @click="toRouterPage('/newsCenter')">消息中心 ></mu-button>
      <mu-flex justify-content="center" align-items="center" style="margin-top: 40px">
        <mu-button class="toHrefbtn" style="margin-bottom: 17px;" large color="tealA700" @click="goLogout()">退出登录</mu-button>
      </mu-flex>
    </div>
    </div>


</template>
<script>
  import {get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
  // 地址选择器组件
  import addressSelect from '../admin/address'
  // import store from '../assets/js/store.js'
  const date = 'Mon Oct 17 2016 00:00:00 GMT+0800 (中国标准时间)'
  export default {
      name:'home',
      // 组件
      components: {
        'address-select' : addressSelect
      },
      data: function () {
        return {
          loading1:false,
          healthname:''
        }
      },
      watch: {    // 监控  监听checkbox事件
        ruleForm: {
          handler: function (val,oldVal) {

          },
          deep: true // 深度监控，可以观察到ruleForm内任意属性的变化
        }
      },
      methods: {
        toRouterPage(page){
          this.loading1 = !this.loading1;
          //setTimeout(() => {
          this.$router.push({path:page});
          this.loading1 = !this.loading1;
          //},200)


        },
        getHealthmanInfo(){
          let elThis = this;
          let data = '';
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/healthSupervisorInfo', this.$qs.stringify(data)).then((res) => {
            if(res.success){
              elThis.healthname = res.data.name;
              //elThis.$router.push({path:'/Login'})
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        goLogout(){
          let elThis = this;
          let data = '';
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/logout', this.$qs.stringify(data)).then((res) => {
            if(res.success){
              elThis.$router.push({path:'/Login'})
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        }
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'newsCenter'){

        //to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        //to.meta.isBack = true;//返回的
      }
      next();
    },
      // 页面初始化
      created(){
        this.getHealthmanInfo();
      }
    }
</script>
<style>
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #ddd;
  }
  .homeTop{
    border-radius: 0;
    width: 100%;
    margin-left:0;
    overflow:hidden;
    margin-top: 0;
    padding-bottom: 2rem;
    background: url('../img/usercenterBackNew.png') no-repeat;
    background-size: 100% 100%;
    margin-bottom: 1rem;
  }
 /* .homeList{
    padding: 10px 20px;
  }*/
  .toHrefbtn{
    margin-bottom: 17px;
  }
  .head {
    width: 5.3rem;
    margin: 0 auto;
    text-align: center;
    position: relative;
    padding-top: 3px;
    margin-bottom: 0;
    margin-top: .7rem;
  }
  .head img{
    width: 100%;
    margin-bottom: -.55rem;
  }
</style>



