import {post} from '../../../axios/ajax'
import Vue from 'vue'
const loginBaseUrl = '';
const elThis = Vue.prototype.SEVERADDR;
export const loginUrls = {
  login: elThis + '/salesman/login/submit',
  logout: loginBaseUrl + '/logout'
}
/**
 * 登录请求
 * @param params
 */
export const requestLogin = params => { return console.log(Vue.prototype.SEVERADDR); post(loginUrls.login, params) }
/**
 * 登出请求
 * @param params
 */
export const requestLogout = params => { return post(loginUrls.logout, params) }



