<template>
  <div class="homeDiv"  style="background-color: #f0f0f0">
    <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
    <mu-container style="padding: 0">
      <mu-tabs :value.sync="active2" inverse color="tealA700" indicator-color="tealA700" full-width style="background-color: white;position: fixed;top: 47px">
        <mu-tab>用户消息</mu-tab>
       <!-- <mu-tab>提示消息</mu-tab>-->
      </mu-tabs>
      <div class="demo-text" v-if="active2 === 0">
        <chatList @toClearChat="toClearChat" style="margin-top: 95px"></chatList>
      </div>
     <!-- <div class="demo-text" v-if="active2 === 1">
        <patientDetailBaogao style="margin-top: 95px"></patientDetailBaogao>
      </div>-->
    </mu-container>
    </div>


</template>
<script>
  import {get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
  // 地址选择器组件
  import patientDetailBaogao from '../healthPageHome/patientDetailBaogao'
  import chatList from '../healthPageHome/chatList'
  import topheader from "../healthPageHome/topheader";
  // import store from '../assets/js/store.js'
  export default {
      name:'patientDetail',
      // 组件
      components: {
        topheader,
        chatList,patientDetailBaogao
      },
      data: function () {
        return {
          backHrefValZhi:'/home',
          topName:'消息中心',
          userpic:'',
          sex:'',
          age:'',
          intimeNewList:null,
          personalInformationPercent:'',
          basicInfoPercent:'',
          userLifePercent:'',
          name:'',
          doctor:'',
          nurse:'',
          active2:0,
          isFirstEnter:false // 是否第一次进入，默认false
        }
      },
      methods: {
        toRouterPage(page){
          this.$router.push({path:page});
        },
        backHrefVal(data){
          this.$router.push({path:data});
        },
        toClearChat(data){
          console.log('父组件的值' + data);
          this.intimeNewList = data;
        },
        activeChange(data){
          console.log(data)
          this.active2 = Number(data);
        },
        getUserBaseInfo(){
          let elThis = this;
          let data = {};
          data.userId = this.$route.query.id;
          get(this.SEVERADDR + '/health_supervisor/health/document/baseInfo', {params:data}).then((res) => {
            if(res.success){
              elThis.doctor = res.data.doctors[0].name;
              elThis.nurse = res.data.nurses[0].name;
              elThis.userpic = res.data.fullHeadImgUrl;
              elThis.age = res.data.age;
              elThis.sex = res.data.sex;
              elThis.personalInformationPercent = res.data.personalInformationPercent;
              elThis.basicInfoPercent = res.data.basicInfoPercent;
              elThis.userLifePercent = res.data.userLifePercent;
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        }
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'Home'){
        to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        to.meta.isBack = true;//返回的
      }
      next();
    },
    beforeRouteLeave(to, from, next) {
      console.log('取消的定时器'+this.intimeNewList)
      clearInterval(this.intimeNewList);　　// 清除定时器
      //this.intime = null;
      next();
    },
    activated(){
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        //this.getUserBaseInfo();
        console.log(this.$route.query.name);
        this.active2 = 0;
      }
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter=false;
    },
      // 页面初始化
      created(){
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      }
    }
</script>
<style>
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
   /* background-color: white;*/
  }
</style>



