const router = new Router({
    routes: [
      {
        path:'/listNews',
        name:'listNews',
        component:listNews,
        meta:{
          keepAlive:true,
          isBack:false
        }
      },
    {
      path:'/Home',
      name:'Home',
      component:Home,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/addPatient',
      name:'addPatient',
      component:addPatient,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/patientList',
      name:'patientList',
      component:patientList,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/patientDetail',
      name:'patientDetail',
      component:patientDetail,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/patientDetailBaseInfo',
      name:'patientDetailBaseInfo',
      component:patientDetailBaseInfo,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/patientDetailLife',
      name:'patientDetailLife',
      component:patientDetailLife,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
      {
        path:'/patientBodyInfo',
        name:'patientBodyInfo',
        component:patientBodyInfo,
        meta:{
          keepAlive:true,
          isBack:false
        }
      },
    {
      path:'/patientDetailHealthInfo',
      name:'patientDetailHealthInfo',
      component:patientDetailHealthInfo,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/postPhotos',
      name:'postPhotos',
      component:postPhotos,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/postPhotosTijian',
      name:'postPhotosTijian',
      component:postPhotosTijian,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/patientDetailCheckXueya',
      name:'patientDetailCheckXueya',
      component:patientDetailCheckXueya,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/patientDetailCheckXuetang',
      name:'patientDetailCheckXuetang',
      component:patientDetailCheckXuetang,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/patientDetailCheckXuezhi',
      name:'patientDetailCheckXuezhi',
      component:patientDetailCheckXuezhi,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/Login',
      name:'Login',
      component:Login,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/newsCenter',
      name:'newsCenter',
      component:newsCenter,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/chat',
      name:'chat',
      component:chat,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/accessUser',
      name:'accessUser',
      component:accessUser,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/accessUserAdd',
      name:'accessUserAdd',
      component:accessUserAdd,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path:'/accessUserOneHistory',
      name:'accessUserOneHistory',
      component:accessUserOneHistory,
      meta:{
        keepAlive:true,
        isBack:false
      }
    },
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/distribute',
      name: 'distribute',
      component: distribute,
      // 配置子路由
      children:[
        // 路径为'/activePublic'，使用组件step1
        { path: ''      , component: step1  },
        // 路径为'/activePublic/step1'，使用组件step1
        { path: 'step1', component: step1  },
        // 路径为'/activePublic/step2'，使用组件step2
       { path: 'step2', component: step2  },
        { path: 'step3', component: step3  },
      ]
    },
    {
      path: '/activeList',
      name: 'activeList',
      component: activeList,
      meta:{keepAlive: true}
    },
    {
      path: '/swiper',
      name: 'swiper',
      component: swiper,
      meta:{keepAlive: true}
    },
    {
      path: '/activePublic',
      name: 'activePublic',
      component: activePublic
    }
  ]
})
