<template>
      <div class="homeDiv">
        <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
        <div class="mescroll1DivAdd">
          <div class="caseDiv set_box_shadow">
            <div class="hos_list_top clearfix">
              <div class="orderContent">
                <div class="left" style=""><img style="width: 80%;border-radius: 50%;margin: 1rem 0 0 .2rem;" :src="fullHeadImgUrl"></div>
                <div class="right"><p class="detail" style="margin-top: .6rem"><span class="name" style="font-size: 1.05rem"></span>
                </p>
                  <p class="orderdetailchoose" style="color: #999"><span>{{username}}</span>
                    <a :href="'tel:' + mobile"><span class="orderdetailchooseName">{{mobile}}</span></a>
                  </p>
                  <p class="orderdetailchoose" style="color: #999"><span>需回访时间：</span> <span class="orderdetailchooseName">{{fullReservationDate}}</span>
                  </p>
                  <p class="detail" style="line-height: 1rem;color: #999;    height: auto;"><span
                    class="name overflow_num_ellipsis">上门地址：{{homeAddress}}</span></p>
                  <p class="detail" style="line-height: 1rem;color: #999;    height: auto;"><span
                    class="name overflow_num_ellipsis">预约事项：{{matter}}</span></p>
                </div>
              </div>
            </div>
          </div>
          <div style="clear: both"></div>
          <div class="hos_child">
            <div class="hos_list_top clearfix">
              <div class="left">回访状态</div>
              <div class="right">
                <select class="community" v-model="accessValue" @change="chooseAccess">
                  <option v-for="item in accessOption" :value="item.id">{{item.name}}</option>
                </select>
              </div>
            </div>
            <div class="orderBottom" style="height: inherit;">
              <p style="font-size: 1.03rem;line-height: 2.5rem;padding-left: 5.2%;color: #555;margin: 0">回访内容</p>
              <textarea placeholder="请输入本次回访内容、备注" v-model="breif"></textarea>
            </div>
            <div v-if="nextAccess == true">
            <div class="hos_list_top clearfix">
              <div class="left">下次回访</div>
              <div class="right">
                <input id="bornDate" ref="button" @click="open = !open" v-model="dateResult" placeholder="请选择下次回访时间" type="text">
                <mu-popover cover :open.sync="open" placement="right" :trigger="trigger">
                  <mu-container>
                    <mu-flex justify-content="between" align-items="end" wrap="wrap">
                      <mu-paper :z-depth="1" class="demo-date-picker">
                        <mu-date-picker :date.sync="date" :min-date="minDate"></mu-date-picker>
                      </mu-paper>
                    </mu-flex>
                    <button class="sureTime" @click="chooseTime">确认</button>
                  </mu-container>
                </mu-popover>
              </div>
            </div>
            <div class="hos_list_top clearfix">
              <div class="left">回访事项</div>
              <div class="right"><input class="hosName" v-model="nextAccessVal" placeholder="请输入下次回访事项" type="text"></div>
            </div>
            </div>
          </div>
          <div class="orderBottom" style="height: inherit;padding-bottom: 1rem;    margin-top: -.5rem;">

          </div>
        </div>
        <button class="ordercommont postBingli" v-if="buttonShow" @click="addAccess">提交上传</button>
      </div>
</template>
<script>
  import {get,post} from "../axios/ajax";
  import {ajaxError,formatDate} from "../assets/js/assist";
  import topheader from "../healthPageHome/topheader";
  import Vue from 'vue'
  export default {
      name:'postPhotos',
      // 组件

      data: function () {
        return {
          topName:'回访详情',
          backHrefValZhi:'/accessUser',
          fullHeadImgUrl:'',
          username:'',
          mobile:'',
          fullReservationDate:'',
          homeAddress:'',
          matter:'',
          accessOption:[
            {id:'', name:'请选择'},
          ],
          accessValue:'',
          bornDate:'',
          nextAccessVal:'',
          breif:'',
          imgData: {
            accept: 'image/gif, image/jpeg, image/png, image/jpg',
          },
          open: false,
          trigger: null,
          date:undefined,
          dateResult:'',
          minDate:new Date(),
          buttonShow:true,
          nextAccess:false
        }
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data,query: {id: this.$route.query.id,name:this.$route.query.name}});
        },
        chooseAccess(){
          if(this.accessValue == 7){
            this.nextAccess = true;
          }else{
            this.nextAccess = false;
          }
        },
        getState(){
          let elThis = this;
          let data = '';
          get(this.SEVERADDR + '/goHome/greetState/all', {params:data}).then((res) => {
            console.log(res);

            res.data.forEach(function (value) {
              elThis.accessOption.unshift(value);
            })
            elThis.accessOption.unshift({id:'', name:'请选择'});
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getAccessDetail(){
          let elThis = this;
          let data = {};
          data.id = this.$route.query.id;
            data.reservationId = this.$route.query.reservationId;
          get(this.SEVERADDR + '/health_supervisor/greet/detailTopic', {params:data}).then((res) => {
            elThis.fullHeadImgUrl = res.data.fullHeadImgUrl;
            elThis.username = res.data.name;
            elThis.mobile = res.data.mobile;
            elThis.fullReservationDate = res.data.fullReservationDate;
            elThis.homeAddress = res.data.homeAddress;
            elThis.matter = res.data.matter;
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getReport(){
          let elThis = this;
          let data = '';
          let id = this.$route.query.reportId;
          get(this.SEVERADDR + '/health_supervisor/one/patientReport/' + id, {params:data}).then((res) => {
            console.log(res);
            this.dateResult = formatDate(new Date(res.data.recordTime),'yyyy-MM-dd');
            this.hosName = res.data.medicalInstitution;
            this.breif = res.data.content;
            res.data.imgs.forEach(function (value) {
              elThis.imgs.push(value);
              console.log(elThis.imgs)
            })
          }).catch(function(err){
            ajaxError(err);
          })
        },
        addAccess () {
          let elThis = this;
          if(this.accessValue == ''){
            this.$toast.info('请选择回访状态');
            return
          }if(this.accessValue == 7){
            if(this.dateResult == ''){
              this.$toast.info('请选择回访时间');
              return
            }
            if(this.nextAccessVal == ''){
              this.$toast.info('请输入下次回访事项');
              return
            }
          }
          if(this.breif == ''){
            this.$toast.info('请输入本次回访内容');
            return
          }

          let data = {};
          data.id = this.$route.query.id;
          data.reservationId = this.$route.query.reservationId;
          data.stateId = this.accessValue;
          data.actualGreetDate = formatDate(new Date(),'yyyy-MM-dd hh:mm:ss');
          data.sit = this.breif;
          data.nextGreetType = 'goHome';
          data.nextGreetDate = this.dateResult;
          data.remark = this.nextAccessVal;
          data.homeAddress = this.homeAddress;
          console.log(data)
          post(this.SEVERADDR + '/health_supervisor/greet/goHome/addRecord', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            if(res.success == true){
              this.$toast.success('添加成功');
              setTimeout(function () {
                elThis.$router.push({
                  path: '/accessUser'
                })
              },500)
            }else{
              this.$toast.error(res.data);
            }
            //this.$router.go(0);
            //elThis.loading1 = !elThis.loading1;
            // window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
        chooseTime(){
          this.open = false;
          if(this.date == undefined || this.date == 'undefined'){
            this.dateResult = formatDate(new Date(),'yyyy-MM-dd');
          }else{
            this.dateResult = formatDate(this.date,'yyyy-MM-dd');
          }
        }
      },
    activated(){
        console.log(5)
        this.pageNumber = 1;
        this.getState();
        this.getAccessDetail();
        this.accessOption = [];
        this.dateResult = '';
        this.breif = '';
    },
      // 页面初始化
      created(){
        //console.log(this.$route.query.id);
      },
    components:{
      topheader
    }
    }
  Vue.filter('FormatData', function (value) {
    return  formatDate(new Date(value),'yyyy-MM-dd');
  })
</script>
<style scoped>
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #f0f0f0
  }
  .mescroll1DivAdd {
    background-color: white;
    margin-top: 55px;
  }
  .hos_child {
    /* height: 8rem; */
    padding-top: .2rem;
    padding-bottom: 0.2em;
    /* margin-top: .9rem; */
   /* float: left;*/
    margin-top: 2px;
  }
  .hos_child .hos_list_top {
    height: 2.9rem;
    /* border: 1px solid red; */
    font-size: 1rem;
    line-height: 2.5rem;
    /* display: flex; */
    float: left;
    width: 95%;
    color: #333333;
    margin-left: 2.8%;
    border-bottom: 1px solid #e5e5e5;
  }
  .hos_list_top .left {
    width: 25%;
    margin: -.2rem 0 0 -0.5%;
    line-height: 3.3rem;
    padding-left: 3%;
    color: #555;
    float: left;
  }
  .hos_list_top .right {
    padding-left: .2rem;
    margin-top: .2em;
    width: 71%;
    float: left;
  }
.right p {
    margin: 0;
  margin-bottom: .3rem;
  }
  .right select {
    width: 70%;
    height: 1.7rem;
    line-height: 1.7rem;
    border: 1px solid #c5c5c5;
    color: #666;
  }
  .right input[type="text"] {
    width: 90%;
    height: 2rem;
    line-height: 2rem;
    border: none;
    outline: none;
    font-size: .85rem;
  }
  .orderBottom {
    width: 100%;
    color: black;
    font-size: .8rem;
    margin: .5rem 0 0 0;
    height: 2.2rem;
    line-height: 1.6rem;
    clear: both;
    background-color: white;
  }
  .orderBottom textarea {
    width: 91%;
    border: 1px solid #d8d8d8;
    /* border: none; */
    font-size: .9rem;
    height: 7rem;
    resize: none;
    outline: none;
    padding: .5rem;
    margin: 0 0 0 4.5%;
    background-color: #f3f3f3;
  }
  .postImgP{
    width: 6rem;
    height: 6rem;
    line-height: 6rem;
    display: inline-block;
    text-align: center;
    border: 1px solid #e5e5e5;
    position: relative;
    margin-right: .7rem;
    vertical-align: top;
    margin-top: 1rem;
  }
  .famousName {
    display: inline-block;
    font-size: .92rem;
    margin-left: 4%;
  }
  .addPhontBtn {
    display: inline-block;
    width: 6rem;
    height: 6rem;
    /* border: 1px solid #dcdcdc; */
    text-align: center;
    vertical-align: top;
    margin-top: 1rem;
    position: relative;
    /* float: right; */
    margin-right: 0;
    background-color: #f1f1f1;
  }
  .addPhontBtn input[type="file"] {
    position: absolute;
    right: 4.3%;
    width: 6rem;
    height: 6rem;
    opacity: 0;
  }
  .addPhontBtn img {
    width: 1.8rem;
    margin: 2rem 0 -.2rem 0;
  }
  .postImg{
    max-width: 3.3rem;
    max-height: 3.3rem;
    margin: .1rem .1rem 0 .1rem;
  }
  .closePhoto{
    position: absolute;
    right: -.6rem;
    top: -.6rem;
    width: 1.3rem;
    background-color: white;
  }
  .warnPhoto {
    color: red;
    font-size: .9rem;
    vertical-align: top;
    display: inline-block;
    margin-top: 3rem;
  }
  .ordercommont {
    border-radius: 5px;
    color: white;
    font-weight: 500;
    font-size: .8rem;
    float: right;
    margin-left: 1.5%;
    margin-right: 3%;
    border: none;
    background-color: #00afa1;
    outline: none;
    padding: .4rem .7rem;
  }
  .postBingli {
    width: 80%;
    margin-left: 10%;
    height: 2.7rem;
    float: none;
    font-size: 1rem;
    margin-top: 2.7rem;
    margin-bottom: 2rem;
  }
  .sureTime {
    width: 50%;
    margin-left: 25%;
    height: 2.7rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 5px;
    color: white;
    font-weight: 500;
    border: none;
    background-color: #2196f3;
    outline: none;
    font-size: 18px;
    padding: .4rem .7rem;
  }
  .havePhoto{
    width: 100%;
    text-align: center;
    margin-top: 1rem;
  }

</style>



