      </div>
      <div class="hos_list_top clearfix">
        <div class="left">婚姻状况</div>
        <div class="right">
          <input class="sex nan" type="radio" name="sex" value="0" v-model="sex">
          <span class="sexname">未婚</span>
          <input class="sex nv" type="radio" name="sex" value="1" v-model="sex">
          <span class="sexname">已婚</span>
        </div>
      </div>
      <div class="hos_list_top clearfix" style="border: none;height: auto;">
        <div class="left" style="    line-height: 1.3rem;margin-top: .4rem;">医疗费用<br>支付方式</div>
        <div class="right" style="margin-bottom: 0.6rem;">
          <span class="yibaoPay"  v-for="(item,i) in yibaoPayList" :class="{yibaoPayActive:item.bool}" @click="chooseYibao(item,i)">
            {{item.name}}
          </span>
        <!--  <span class="yibaoPay" :class="{'yibaoPayActive':true}">城镇职工基本医疗保险</span>
          <span class="yibaoPay">城镇居民基本医疗保险</span>
          <span class="yibaoPay">新型农村合作医疗</span>
          <span class="yibaoPay">贫困救助</span>
          <span class="yibaoPay">商业医疗保险</span>
          <span class="yibaoPay">全公费</span>
          <span class="yibaoPay">综合医疗保险</span>
          <span class="yibaoPay">全自费</span>
          <span class="yibaoPay">住院医疗保险</span>
          <span class="yibaoPay">农民工医疗保险</span>
          <span class="yibaoPay">少儿住院及门诊医保</span>
          <span class="yibaoPay">其他</span>-->
        </div>
      </div>
    </div>
    <button class="bindBtn" @click="postUserPersonalInfo">{{sureBtn}}</button>
  </div>

</template>
<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
  import topheader from "../healthPageHome/topheader";
  export default {
      name:'patientDetailBaseInfo',
      data: function () {
        return {
          topName:'个人基本信息',
          backHrefValZhi:'/patientDetail',
          mobile:'',
          idcard:'',
          address:'',
          nation:'请选择',
          xueli:'请选择',
          job:'请选择',
          sex:'0',
          yibaoPayList:[
            {name:'城镇职工基本医疗保险',bool:false},
            {name:'城镇居民基本医疗保险',bool:false},
            {name:'新型农村合作医疗',bool:false},
            {name:'贫困救助',bool:false},
            {name:'商业医疗保险',bool:false},
            {name:'全公费',bool:false},
            {name:'综合医疗保险',bool:false},
            {name:'全自费',bool:false},
            {name:'住院医疗保险',bool:false},
            {name:'农民工医疗保险',bool:false},
            {name:'少儿住院及门诊医保',bool:false},
            {name:'其他',bool:false}
          ],
          sureBtn:'确认',
          isFirstEnter:false // 是否第一次进入，默认false
        }
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data,query:{id:this.$route.query.id,name:this.$route.query.name}});
        },
        chooseYibao(item,i){
          item.bool = !item.bool;
        },
        getUserPersonalInfo(){
          let elThis = this;
          let data = {};
          data.userId = this.$route.query.id;
          get(this.SEVERADDR + '/health_supervisor/userPersonalInformation', {params:data}).then((res) => {
            if(res.success){
              elThis.mobile = res.data.mobile;
              elThis.idcard = res.data.idCard || '';
              elThis.address = res.data.familyAddress;
              elThis.xueli = res.data.education || '请选择';
              elThis.nation = res.data.nationality || '请选择';
              elThis.sex = res.data.maritalStatus;
              elThis.job = res.data.occupation || '请选择';
              if(res.data.medicalExpensesPaymentMethod && res.data.medicalExpensesPaymentMethod != ''){
                this.yibaoPayList.forEach((value) => {
                  value.bool = false
                  if(res.data.medicalExpensesPaymentMethod.split('|').indexOf(value.name) != -1){
                    value.bool = true
                  }
                });
              }
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        postUserPersonalInfo() {
          let elThis = this;
          let data = {};
         data.userId = this.$route.query.id;
          data.familyAddress = this.address;
          data.idCard = this.idcard;
          data.nationality = this.nation;
          data.education = this.xueli;
          data.maritalStatus = this.sex;
          data.occupation = this.job;
          let lastChooseYibao = [];
          this.yibaoPayList.forEach((value) => {
            if(value.bool == true){
              lastChooseYibao.push(value.name);
            }
          });
          data.medicalExpensesPaymentMethod = lastChooseYibao.join('|');
          console.log(data)
          post(this.SEVERADDR + '/health_supervisor/edit/userPersonalInformation', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            this.$toast.success('个人基本信息完善成功');
            if(this.$route.query.write == true || this.$route.query.write == 'true'){
              this.$router.push({
                path: '/patientDetailHealthInfo',
                query: {id:this.$route.query.id,name:this.$route.query.name,write:true}
              })
            }
            //window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'patientDetail'){
        to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        to.meta.isBack = true;//返回的
      }
      next();
    },
    activated(){
        console.log(this.$route.query.write,typeof (this.$route.query.write))
      if(this.$route.query.write == true || this.$route.query.write == 'true'){
          this.sureBtn = '下一步';
      }else{
        this.sureBtn = '确认';
      }
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        this.getUserPersonalInfo();
      }
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter=false;
    },
      // 页面初始化
      created(){
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      },
      components:{
        topheader
      }
    }
</script>
<style scoped>
  .addPatient{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #ebebeb;
  }
  .hos_child {
    margin-top: 53px;
    float: left;
    width: 100%;
    margin-left: 0;
    border-radius: 0;
    padding: 0 .2rem;
    padding-bottom: 0.2em;
    background-color: #ffffff;
  }
  .hos_child .hos_list_top {
    height: 2.9rem;
    /* border: 1px solid red; */
    font-size: 1rem;
    line-height: 2.5rem;
    /* display: flex; */
    float: left;
    width: 95%;
    color: #333333;
    margin-left: 2.8%;
    /* border-bottom: 1px solid #e5e5e5; */
  }
  .hos_list_top .left {
    width: 26%;
    margin: -.2rem 0 0 -0.5%;
    line-height: 3.3rem;
    padding-left: 3%;
    color: #555;
    float: left;
    font-size: .92rem;
  }
  .hos_list_top .right {
    padding-left: .2rem;
    margin-top: .2em;
    width: 70%;
    float: left;
  }
  .right input[type="text"] {
    width: 100%;
    height: 1.9rem;
    line-height: 2rem;
    border: 1px solid #bfbfbf;
    padding-left: 2%;
    outline: none;
    font-size: .85rem;
    border-radius: 3px;
  }
  .right select {
    width: 100%;
    height: 1.9rem;
    line-height: 1.7rem;
    border: 1px solid #a2a2a2;
    color: #5a5a5a;
    border-radius: 4px;
  }
  .sex:checked {
    background: url(../img/raido.png);
    background-size: 100% 100%;
    color: white;
    border: 0px;
  }
  .sex {
    float: left;
    width: 15px;
    margin-left: 12px;
    height: 15px;
    margin-top: .8rem;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background: #fff;
    border-radius: 50%;
    border: 1px solid #c0c0c0;
    color: #fff;
    display: block;
  }
  .sexname {
    float: left;
    margin: 0 2em 0 5px;
  }
  .yibaoPay{
    padding: 0 .4rem;
    height: 1.9rem;
    line-height: 1.9rem;
    border: 1px solid #c5c5c5;
    color: #555;
    font-size: .85rem;
    border-radius: 3px;
    display: inline-block;
    margin-right: .6rem;
  }
  .yibaoPayActive{
    background-color: #00afa1;
    color: white;
    border: 1px solid #00afa1;
  }
  .bindBtn {
    width: 52%;
    margin-left: 24%;
    border-radius: 5px;
    height: 2.5rem;
    line-height: 2.5rem;
    color: white;
    font-size: 1rem;
    margin-top: 6%;
    text-align: center;
    /* position: fixed; */
    bottom: 0;
    background-color: #00afa1;
    margin-bottom: 2rem;
    border: none;
  }
  .yibaoPayActive{
    background-color: #00afa1;
    color: white;
    border: 1px solid #00afa1;
  }
</style>



