<template>
      <div class="homeDiv">
        <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
        <div class="caseDiv set_box_shadow" style="margin-top: 50px">
          <div class="hos_list_top clearfix">
            <div class="orderContent">
              <div class="left" style=""><img style="width: 75%;border-radius: 50%;margin: 1rem 0 0 .2rem;" :src="fullHeadImgUrl"></div>
              <div class="right"><p class="detail" style="margin-top: .6rem"><span class="name" style="font-size: 1.05rem"></span>
              </p>
                <p class="orderdetailchoose" style="color: #999"><span>{{username}} </span>
                  <a :href="'tel:' + mobile"><span class="orderdetailchooseName">{{mobile}}</span></a>
                </p>
                <p class="orderdetailchoose" style="color: #999"><span>需回访时间：</span> <span class="orderdetailchooseName">{{fullReservationDate}}</span>
                </p>
                <p class="detail" style="line-height: 1rem;color: #999;    height: auto;"><span
                  class="name overflow_num_ellipsis">上门地址：{{homeAddress}}</span></p>
                <p class="detail" style="line-height: 1rem;color: #999;    height: auto;"><span
                  class="name overflow_num_ellipsis">预约事项：{{matter}}</span></p>
              </div>
              <div style="clear: both"></div>
            </div>
          </div>
        </div>
        <div style="clear: both"></div>
          <div class="hos_child" v-for="(i,index) in list">
            <div class="hos_list_top clearfix">
              <div class="left">回访时间</div>
              <div class="right">
                {{i.actualGreetDate | FormatDataTime}}
              </div>
            </div>
            <div class="hos_list_top clearfix">
              <div class="left">回访状态</div>
              <div class="right">
                {{i.stateName}}
              </div>
            </div>
            <div class="hos_list_top clearfix">
              <div class="left">回访内容</div>
              <div class="right">
                {{i.greetSit}}
              </div>
            </div>
            <div class="hos_list_top clearfix" v-if="i.nextGreetDate">
              <div class="left">下次回访时间</div>
              <div class="right">
                {{i.nextGreetDate | FormatDataTime}}
              </div>
            </div>
            <div class="hos_list_top clearfix" v-if="i.remark">
              <div class="left">下次回访事项</div>
              <div class="right">
                {{i.remark}}
              </div>
            </div>
            <div style="clear: both;"></div>
          </div>
      </div>
</template>
<script>
  import {get,post} from "../axios/ajax";
  import {ajaxError,formatDate} from "../assets/js/assist";
  import topheader from "../healthPageHome/topheader";
  import Vue from 'vue'
  export default {
      name:'accessUserOneHistory',
      // 组件

      data: function () {
        return {
          topName:'历史记录',
          backHrefValZhi:'/accessUser',
          fullHeadImgUrl:'',
          username:'',
          mobile:'',
          fullReservationDate:'',
          homeAddress:'',
          matter:'',
          list:[]
        }
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data,query: {id: this.$route.query.id,name:this.$route.query.name}});
        },
        getHistory(){
          let elThis = this;
          let data = {};
          data.id = this.$route.query.id;
          data.reservationId = this.$route.query.reservationId;
          get(this.SEVERADDR + '/health_supervisor/greet/history', {params:data}).then((res) => {
            console.log(res);
            elThis.fullHeadImgUrl = res.data.fullHeadImgUrl;
            elThis.username = res.data.name;
            elThis.mobile = res.data.mobile;
            elThis.fullReservationDate = res.data.fullReservationDate;
            elThis.homeAddress = res.data.homeAddress;
            elThis.matter = res.data.matter;
            if(res.data.history && res.data.history.length > 0){
              res.data.history.forEach(function (value) {
                elThis.list.push(value)
              })
            }


          }).catch(function(err){
            ajaxError(err);
          })
        },
      },
    activated(){
        this.list = [];
        this.getHistory();
    },
      // 页面初始化
      created(){
        //console.log(this.$route.query.id);
      },
    components:{
      topheader
    }
    }
</script>
<style scoped>
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: white
  }
  .mescroll1DivAdd {
    background-color: white;
    margin-top: 55px;
  }
  .orderContent{
    border-bottom: 1px solid #dddddd;
  }
  .hos_child {
    /* height: 8rem; */
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    /* margin-top: .9rem; */
   /* float: left;*/
    margin-top: 2px;
    border-bottom: 1px solid #dddddd;
  }
  .hos_child .hos_list_top {
    height: 1.6rem;
    /* border: 1px solid red; */
    line-height: 1rem;
    /* display: flex; */
    float: left;
    width: 95%;
    color: #333333;
    margin-left: 2.8%;
    /*border-bottom: 1px solid #e5e5e5;*/
    font-size: 14px;
  }
  .hos_list_top .left {
    width: 28%;
    margin: 0 0 0 -0.5%;
    line-height: 1.4rem;
    padding-left: 3%;
    color: #555;
    float: left;
  }
  .hos_list_top .right {
    padding-left: .2rem;
    margin-top: .2em;
    width: 71%;
    float: left;
  }
.right p {
    margin: 0;
  margin-bottom: .3rem;
  }
  .right select {
    width: 70%;
    height: 1.7rem;
    line-height: 1.7rem;
    border: 1px solid #c5c5c5;
    color: #666;
  }
  .right input[type="text"] {
    width: 90%;
    height: 2rem;
    line-height: 2rem;
    border: none;
    outline: none;
    font-size: .85rem;
  }
  .orderBottom {
    width: 100%;
    color: black;
    font-size: .8rem;
    margin: .5rem 0 0 0;
    height: 2.2rem;
    line-height: 1.6rem;
    clear: both;
    background-color: white;
  }
  .orderBottom textarea {
    width: 91%;
    border: 1px solid #d8d8d8;
    /* border: none; */
    font-size: .9rem;
    height: 7rem;
    resize: none;
    outline: none;
    padding: .5rem;
    margin: 0 0 0 4.5%;
    background-color: #f3f3f3;
  }
  .postImgP{
    width: 6rem;
    height: 6rem;
    line-height: 6rem;
    display: inline-block;
    text-align: center;
    border: 1px solid #e5e5e5;
    position: relative;
    margin-right: .7rem;
    vertical-align: top;
    margin-top: 1rem;
  }
  .famousName {
    display: inline-block;
    font-size: .92rem;
    margin-left: 4%;
  }
  .addPhontBtn {
    display: inline-block;
    width: 6rem;
    height: 6rem;
    /* border: 1px solid #dcdcdc; */
