    margin-bottom: 2rem;
  }
  .sureTime {
    width: 50%;
    margin-left: 25%;
    height: 2.7rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 5px;
    color: white;
    font-weight: 500;
    border: none;
    background-color: #2196f3;
