<template>
      <div class="demo-text">
        <div id="mescroll3" class="mescroll">

            <div class="hos_list_top clearfix" v-for="(i,index) in xueyaList">
              <div class="yuyueTop">
                <span class="famousName hosName">血压</span>
              </div>
              <div class="orderContent">
                <button class="ordercommont" @click="toXue('/patientDetailCheckXueya')">添加新数据&gt;</button>
                <div class="left"><img src="../img/xueya.png"></div>
                <div class="right">
                  <p class="detail">
                    <span class="name">高压 {{i.maximumPressure}}mmHg</span>
                  </p>
                  <p class="detail">
                    <span class="name">低压 {{i.minimumPressure}}mmHg</span>
                  </p>
                  <p class="detail" style="margin-bottom: .45rem;">
                    <span class="name">心率 {{i.heartRate}}mmHg</span>
                  </p>
                  <p class="orderdetailchoose" style="    margin-bottom: .5rem;">
                    <span>测量时间：</span>
                    <span class="orderdetailchooseName">{{i.monitoringDatetime}}</span>
                  </p>
                </div>
              </div>
              <div class="clear"></div>
            </div>

            <div class="hos_list_top clearfix"  v-for="(i,index) in xuetangList">
              <div class="yuyueTop">
                <span class="famousName hosName">血糖</span>
              </div>
              <div class="orderContent">
                <button class="ordercommont" @click="toXue('/patientDetailCheckXuetang')">添加新数据&gt;</button>
                <div class="left"><img src="../img/xuetang.png"></div>
                <div class="right">
                  <p class="detail" v-for="(iDay,indexDay) in i.detailList">
                    <span class="name">{{iDay.timeBucket}} {{iDay.bloodGlucose}}mmHg</span>
                  </p>
                  <p class="orderdetailchoose" style="    margin-bottom: .5rem;">
                    <span>测量时间：</span>
                    <span class="orderdetailchooseName">{{i.detailList[0].monitoringDatetime | getDayReturn}}</span>
                  </p>
                </div>
              </div>
              <div class="clear"></div>
            </div>


            <div class="hos_list_top clearfix" v-for="(i,index) in xuezhiList">
              <div class="yuyueTop">
                <span class="famousName hosName">血脂</span>
              </div>
              <div class="orderContent">
                <button class="ordercommont" @click="toXue('/patientDetailCheckXuezhi')">添加新数据&gt;</button>
                <div class="left"><img src="../img/xuezhi.png"></div>
                <div class="right">
                  <p class="detail">
                    <span class="name">总胆固醇 {{i.totalCholesterol}}mmHg</span>
                  </p>
                  <p class="detail">
                    <span class="name">低密度脂蛋白 {{i.ldl}}mmHg</span>
                  </p>
                  <p class="detail" style="margin-bottom: .45rem;">
                    <span class="name">高密度脂蛋白 {{i.hdl}}mmHg</span>
                  </p>
                  <p class="detail" style="margin-bottom: .45rem;">
                    <span class="name">甘油三酯 {{i.triglyceride}}mmHg</span>
                  </p>
                  <p class="orderdetailchoose" style="    margin-bottom: .5rem;">
                    <span>测量时间：</span>
                    <span class="orderdetailchooseName">{{i.monitoringDatetime}}</span>
                  </p>
                </div>
              </div>
              <div class="clear"></div>
            </div>

        </div>
      </div>

</template>
<script>
  import {get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";

  export default {
      name:'patientDetailCheck',
      // 组件
      props:["doctor","nurse","username"], //父组件：doctor
      data: function () {
        return {
          xueyaList:[],
          xuetangList:[],
          xuezhiList:[]
        }
      },
      methods: {
        toXue(val){
          this.$router.push({path:val,query:{id:this.$route.query.id,name:this.$route.query.name}});
        },
        getXueyaList(){
          let elThis = this;
          let data = {};
          data.pageNumber = 1;
          data.pageSize = 1;
          data.userId = this.$route.query.id;
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/bloodPressure/findPageByHealthSupervisorId', {params:data}).then((res) => {
            console.log(res);
            if(res.data.content.length > 0){
              res.data.content.forEach(function (value) {
                elThis.xueyaList.push(value);
              });
            }else{
              elThis.xueyaList.push({});
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getXuetangList(){
          let elThis = this;
          let data = {};
          data.pageNumber = 1;
          data.pageSize = 1;
          data.userId = this.$route.query.id;
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/bloodGlucose/findPage', {params:data}).then((res) => {
            console.log(res);
            if(res.data.content.length > 0){
              res.data.content.forEach(function (value) {
                elThis.xuetangList.push(value);
              });
            }else{
              elThis.xuetangList.push({"detailList":[{}]});
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getXuezhiList(){
          let elThis = this;
          let data = {};
          data.pageNumber = 1;
          data.pageSize = 1;
          data.userId = this.$route.query.id;
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/bloodFat/findPage', {params:data}).then((res) => {
            console.log(res);
            if(res.data.content.length > 0){
              res.data.content.forEach(function (value) {
                elThis.xuezhiList.push(value);
              });
            }else{
              elThis.xuezhiList.push({});
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
      },
    filters: {
      getDayReturn: function (value) {
        console.log(value)
        if(value != undefined){
          return value.split(' ')[0]
        }
      }
    },
      // 页面初始化
      created(){
        this.getXueyaList();
        this.getXuetangList();
        this.getXuezhiList();

        //console.log(this.$route.query.id);
      }
    }
</script>
<style scoped>
  .clear{
    clear: both;
  }
  .hos_list_top {
    font-size: .9rem;
    width: 100%;
    color: #333333;
    background: white;
    margin-top: .9rem;
  }
  .yuyueTop {
    width: 100%;
    color: black;
    font-size: .9rem;
    margin: .1rem 0 0 0%;
    height: 2.2rem;
    line-height: 2rem;
    clear: both;
    border-bottom: solid 1px #dcdcdc;
  }
  .famousName {
    display: inline-block;
    font-size: .92rem;
    margin-left: 4%;
  }
  .orderContent {
    width: 96%;
    color: black;
    font-size: .2rem;
    margin: .7rem 0 0 4%;
    height: 5.5rem;
    clear: both;
    /* border-bottom: solid 1px #dcdcdc; */
  }
  .ordercommont {
    border-radius: 5px;
    color: white;
    font-weight: 500;
    font-size: .8rem;
    float: right;
    margin-left: 1.5%;
    margin-right: 3%;
    border: none;
    background-color: #00afa1;
    outline: none;
    padding: .25rem .7rem;
    position: relative;
    z-index: 99;
  }
  .hos_list_top .left {
    width: 19%;
    margin: .1rem 0 0 -0.5%;
    line-height: 3.3rem;
    padding-left: 0;
    color: #222;
    float: left;
  }
  .hos_list_top .left img {
    max-height: 4.3rem;
    overflow: hidden;
    width: initial;
    padding: .2rem;
  }
  .hos_list_top .right {
    padding-left: 6%;
    margin-top: -2rem;
    width: 75%;
    float: left;
  }
  .right p {
    margin-bottom: .2rem;
    height: 1.4rem;
    line-height: 1.4rem;
    margin-top: .3rem;
    font-size: .85rem;
  }

</style>



