<template>
  <div class="addPatient">
    <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
    <mu-container style="margin-top: 47px">
      <mu-row>
        <mu-col span="6"><div class="grid-cell"><mu-text-field :label-position="left" label="患者姓名" v-model="normal.username" style="width: 100%"></mu-text-field></div></mu-col>
        <mu-col span="6"><div class="grid-cell"><mu-text-field label="患者手机号"  v-model="normal.mobile"  style="width: 100%"></mu-text-field></div></mu-col>
      </mu-row>
      <mu-row>
        <mu-col span="6"><div class="grid-cell">
          <mu-select label="性别" v-model="normal.sex" full-width>
            <mu-option v-for="option,index in options" :key="option" :label="option" :value="option"></mu-option>
          </mu-select>
        </div>
        </mu-col>
        <mu-col span="6"><div class="grid-cell"><mu-text-field label="患者年龄"  v-model="normal.age" style="width: 100%"></mu-text-field></div></mu-col>
      </mu-row>
      <mu-row>
        <mu-col span="6"><div class="grid-cell">
          <mu-select label="身高" v-model="normal.high" full-width>
            <mu-option v-for="highitem,index in highoptions" :key="highitem" :label="highitem" :value="highitem"></mu-option>
          </mu-select>
        </div>
        </mu-col>
        <mu-col span="6"><div class="grid-cell">
          <mu-select label="体重" v-model="normal.weight" full-width>
            <mu-option v-for="weightitem,index in weightoptions" :key="weightitem" :label="weightitem" :value="weightitem"></mu-option>
          </mu-select>
        </div>
        </mu-col>
      </mu-row>
      <mu-row>
        <mu-col>
          <mu-select label="所属社区" v-model="normal.community" full-width>
            <mu-option v-for="(communityitem,index) in communityOption" :key="communityitem.name" :label="communityitem.name" :value="communityitem.id"></mu-option>
          </mu-select>
        </mu-col>
      </mu-row>
      <mu-row>
        <mu-col span="6"><div class="grid-cell">
          <mu-select label="医师" v-model="normal.doctor" full-width>
            <mu-option v-for="(doctoritem,index) in doctoroptions" :key="doctoritem.name" :label="doctoritem.name" :value="doctoritem.id"></mu-option>
          </mu-select>
        </div>
        </mu-col>
        <mu-col span="6"><div class="grid-cell">
          <mu-select label="护士" v-model="normal.nurse" full-width>
            <mu-option v-for="(nurseitem,index) in nurseoptions" :key="nurseitem.name" :label="nurseitem.name" :value="nurseitem.id"></mu-option>
          </mu-select>
        </div>
        </mu-col>
      </mu-row>
      <mu-row>
        <mu-col>
          <mu-select label="患者职业" v-model="normal.job" full-width>
            <mu-option v-for="jobitem,index in joboptions" :key="jobitem" :label="jobitem" :value="jobitem"></mu-option>
          </mu-select>
        </mu-col>
      </mu-row>
      <mu-row>
        <mu-col><mu-text-field label="患者地址（选填）" style="width: 100%" v-model="normal.address"></mu-text-field></mu-col>
      </mu-row>
     <!-- <mu-row>
        <mu-col><mu-text-field label="患者咨询事项" style="width: 100%" v-model="normal.zixun"></mu-text-field></mu-col>
      </mu-row>
      <mu-row>
        <mu-col><mu-text-field label="患者诉求" style="width: 100%" v-model="normal.breif"></mu-text-field></mu-col>
      </mu-row>-->
      <mu-flex justify-content="center" v-loading="loading1" data-mu-loading-size="24" @click="addPtient()" align-items="center" style="margin-top: 40px">
        <mu-button full-width color="tealA700">添加</mu-button>
      </mu-flex>
    </mu-container>
  </div>

</template>
<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
  import topheader from "../healthPageHome/topheader";
  export default {
      name:'home',
      data: function () {
        return {
          topName:'添加患者',
          backHrefValZhi:'/home',
          left:'left',
          options:['男','女'],
          highoptions:[],
          weightoptions:[],
          doctoroptions:[],
          nurseoptions:[],
          loading1:false,
          normal:{
            username:'',//姓名
            mobile:'',//手机号
            sex:'', //性别
            age:'',//年龄
            job:'',
            address:'',//地址
            high:'',//身高
            weight:'',//体重
            community:'',//社区
            doctor:'',//医师
            nurse:''//护士
          },
          communityOption:[],
          joboptions:["国家籍贯、党群组织、事业单位","专业技术人员","办事人员和有关人员","商业、服务业人员","农林牧渔水利业人员","生产运输设备操作人员","军人","不便分类的其他从业人员","无职业",],//职业
        }
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data});
        },
        addPtient () {
          if(this.normal.username == '' || this.normal.username.length < 2){
            this.$toast.info('请输入正确的患者姓名',{time:10000});
            return
          }
          if(this.normal.mobile.length != 11){
            this.$toast.info('请输入正确的患者手机号');
            return
          }
          this.loading1 = !this.loading1;
          let elThis = this;
          let data = {};
          data.fullname = this.normal.username;
          data.mobile = this.normal.mobile;
          if(this.normal.sex == '男'){
            data.sex = 'woman';
          }else if(this.normal.sex == '女'){
            data.sex = 'man';
          }else{
            data.sex = 'unknown';
          }
          data.age = this.normal.age;
          data.occupation = this.normal.job;
          data.familyAddress = this.normal.address;
          data.height = this.normal.high.split('cm')[0];
          data.weight = this.normal.weight.split('kg')[0];
          data.communityId = this.normal.community;
          data.communityDoctorId = this.normal.doctor;
          data.communityNurseId = this.normal.nurse;
          console.log(data)
          post(this.SEVERADDR + '/health_supervisor/edit/patient', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            if(res.success){
              this.$toast.success('添加成功');
              /*elThis.normal.username = '';
              elThis.normal.mobile = '';
              elThis.normal.sex = '';
              elThis.normal.age = '';
              elThis.normal.job = '';
              elThis.normal.address = '';
              elThis.normal.high = '';
              elThis.normal.weight = '';
              elThis.normal.community = '';
              elThis.normal.doctor = '';
              elThis.normal.nurse = '';*/
              setTimeout(function () {
                elThis.$router.push({
                  path: '/patientDetailBaseInfo',
                  query: {id: res.data.id,name:res.data.fullname,write:true}
                })
              },300);
            }else{
              this.$toast.error(res.data);
            }
            elThis.loading1 = !elThis.loading1;
           // elThis.$router.push({path:'/patientList'})
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getCommunity(){
          let elThis = this;
          let data = '';
          get(this.SEVERADDR + '/community/json/list', {params:data}).then((res) => {
            if(res.success){
              res.data.forEach(function (value) {
                elThis.communityOption.push(value);
              })
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getDoctor(){
          let elThis = this;
          let data = {};
          data.roleName = '医生';
          get(this.SEVERADDR + '/communityUserRole/all/search_box/byRole', {params:data}).then((res) => {
            if(res.success){
              res.data.forEach(function (value) {
                elThis.doctoroptions.push(value);
              })
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getNurse(){
          let elThis = this;
          let data = {};
          data.roleName = '护士';
          get(this.SEVERADDR + '/communityUserRole/all/search_box/byRole',{params:data}).then((res) => {
            if(res.success){
              res.data.forEach(function (value) {
                elThis.nurseoptions.push(value);
              })
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        }
      },
      activated(){

        if(this.$route.query.name != undefined){
            if(isNaN(this.$route.query.name)){ //不全是数字
              this.normal.username = this.$route.query.name;
            }else{
              this.normal.mobile = this.$route.query.name;
            }
        }
      },
      // 页面初始化
      created(){
        for(let i=0;i<=130;i++){
          this.highoptions.push(100+i + 'cm');
        }
        for(let i=0;i<=100;i++){
          this.weightoptions.push(30+i + 'kg');
        }
        this.getDoctor();
        this.getNurse();
        this.getCommunity();
        console.log('......................................我试试看')
      },
      components:{
        topheader
      }
    }
</script>
<style>
  .addPatient{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: white;
  }
</style>



