<template>

	<el-form ref="form" :model="form" label-width="80px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
    <div>
      <p>FullName: {{fullName}}</p>
      <!--<p>FirstName: <input :class="{'active':activeClass}" type="text" v-model="firstName"></p>-->
    </div>
    <el-form-item label="fullName">
      <el-input v-model="firstName"></el-input>
    </el-form-item>
		<el-form-item label="活动名称">
			<el-input v-model="form.name"></el-input>
		</el-form-item>
		<el-form-item label="活动区域">
			<el-select v-model="form.region" placeholder="请选择活动区域">
				<el-option label="区域一" value="shanghai"></el-option>
				<el-option label="区域二" value="beijing"></el-option>
			</el-select>
		</el-form-item>
		<el-form-item label="活动时间">
			<el-col :span="11">
				<el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
			</el-col>
			<el-col class="line" :span="2">-</el-col>
			<el-col :span="11">
				<el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
			</el-col>
		</el-form-item>
		<el-form-item label="即时配送">
			<el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
		</el-form-item>
		<el-form-item label="活动性质">
			<el-checkbox-group v-model="form.type">
				<el-checkbox label="canting" name="type">美食/餐厅线上活动</el-checkbox>
				<el-checkbox label="ditui"  name="type">地推活动</el-checkbox>
				<el-checkbox label="xianxia" name="type">线下主题活动</el-checkbox>
				<el-checkbox label="pinpai" name="type">单纯品牌曝光</el-checkbox>
			</el-checkbox-group>
		</el-form-item>
		<el-form-item label="特殊资源">
			<el-radio-group v-model="form.resource">
				<el-radio label="线上品牌商赞助"></el-radio>
				<el-radio label="线下场地免费"></el-radio>
			</el-radio-group>
		</el-form-item>
		<el-form-item label="活动形式">
			<el-input type="textarea" v-model="form.desc"></el-input>
		</el-form-item>
		<el-form-item>
			<el-button type="primary" @click="mustSet">立即创建</el-button>
			<el-button @click="mustClose">取消</el-button>
		</el-form-item>
	</el-form>
</template>

<script>
  import {formatDate} from "../assets/js/assist";

  export default {
	  name:'add',
		data() {
			return {
        firstName: 'Dawei',
        lastName: 'Lou',
        fullName: 'csx',
        activeClass:true,
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
			}
		},
    watch: {
      firstName(newName, oldName) {
        this.fullName = newName + ' ' + this.lastName;
      }
    },
		methods: {
			onSubmit() {
				console.log('submit!');
			},
      mustSet(){
			  let data = {};

			  data.type = this.form.type;
        data.name = this.form.name;
        data.region = this.form.region;
        data.date =  formatDate(this.form.date2,'yyyy-MM-dd hh:mm:ss');

			  console.log(data)

      },
      mustClose(){
        this.form.type = [];
        this.form.name = '';
      }
		}
	}

</script>



