<template>
  <div class="homeDiv"  style="background-color: #f0f0f0">
    <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
    <mu-container style="padding: 0">
      <mu-tabs :value.sync="active2" inverse color="tealA700" indicator-color="tealA700" full-width style="background-color: white;position: fixed;top: 47px">
        <mu-tab>基础信息</mu-tab>
        <mu-tab>就诊病历</mu-tab>
        <mu-tab>体检报告</mu-tab>
        <mu-tab>日常检测</mu-tab>
      </mu-tabs>
      <div class="demo-text" v-if="active2 === 0">
        <patientDetailBase style="margin-top: 95px" :userpic="userpic" :age="age" :sex="sex" :personalInformationPercent="personalInformationPercent" :basicInfoPercent="basicInfoPercent" :userLifePercent="userLifePercent" :userTraitInfoPercent="userTraitInfoPercent" :doctor="doctor" :nurse="nurse" :username="name"  @activeChange="activeChange"></patientDetailBase>
      </div>
      <div class="demo-text" v-if="active2 === 1">
        <patientDetailJiuzhen style="margin-top: 95px"></patientDetailJiuzhen>
      <!--  <postPhotos style="margin-top: 95px"></postPhotos>-->
      </div>
      <div class="demo-text" v-if="active2 === 2">
        <patientDetailBaogao style="margin-top: 95px"></patientDetailBaogao>
      </div>
      <div class="demo-text" v-if="active2 === 3">
        <patientDetailCheck style="margin-top: 95px" :doctor="doctor" :nurse="nurse" :username="name"></patientDetailCheck>
      </div>
    </mu-container>
    </div>
</template>
<script>
  import {get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
  // 地址选择器组件

  import patientDetailBase from '../healthPageHome/patientDetailBase'
  import patientDetailJiuzhen from '../healthPageHome/patientDetailJiuzhen'
  import postPhotos from '../healthPageHome/postPhotos'
  import patientDetailBaogao from '../healthPageHome/patientDetailBaogao'
  import patientDetailCheck from '../healthPageHome/patientDetailCheck'
  import topheader from "../healthPageHome/topheader";
  // import store from '../assets/js/store.js'
  export default {
      name:'patientDetail',
      // 组件
      components: {
        topheader,
        patientDetailBase,patientDetailJiuzhen,postPhotos,patientDetailBaogao,patientDetailCheck

      },
      data: function () {
        return {
          backHrefValZhi:'/patientList',
          topName:'',
          userpic:'',
          sex:'',
          age:'',
          personalInformationPercent:'',
          basicInfoPercent:'',
          userLifePercent:'',
          userTraitInfoPercent:'',
          name:'',
          doctor:'',
          nurse:'',
          active2:0,
          isFirstEnter:false // 是否第一次进入，默认false
        }
      },
      methods: {
        toRouterPage(page){
          this.$router.push({path:page});
        },
        backHrefVal(data){
          this.$router.push({path:data});
        },
        activeChange(data){
          console.log(data)
          this.active2 = Number(data);
        },
        getUserBaseInfo(){
          let elThis = this;
          let data = {};
          data.userId = this.$route.query.id;
          get(this.SEVERADDR + '/health_supervisor/health/document/baseInfo', {params:data}).then((res) => {
            if(res.success){
              if(res.data.doctors.length > 0){
                elThis.doctor = res.data.doctors[0].name;
              }else{
                elThis.doctor = '';
              }
              if(res.data.nurses.length > 0){
                elThis.nurse = res.data.nurses[0].name;
              }else{
                elThis.nurse = '';
              }
              elThis.userpic = res.data.fullHeadImgUrl;
              elThis.age = res.data.age;
              elThis.sex = res.data.sex;
              elThis.personalInformationPercent = res.data.personalInformationPercent || 0;
              elThis.basicInfoPercent = res.data.basicInfoPercent || 0;
              elThis.userLifePercent = res.data.userLifePercent || 0;
              elThis.userTraitInfoPercent = res.data.userTraitInfoPercent || 0;
            }else{
              elThis.$toast.error(res.data);
            }
          }).catch(function(err){
            ajaxError(err);
          })
        }
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'patientList'){
        to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        to.meta.isBack = true;//返回的
      }
      next();
    },
    activated(){
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        this.getUserBaseInfo();
        console.log(this.$route.query.name);
        this.active2 = 0;
        this.name = this.$route.query.name;
        this.topName = this.$route.query.name + ' 的健康档案';
      }
      if(this.$route.query.write == true || this.$route.query.write == 'true'){
        this.active2 = 3;
      }
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter=false;
    },
      // 页面初始化
      created(){
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      }
    }
</script>
<style>
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
   /* background-color: white;*/
  }
</style>



