<template>
  <div>
    <!--标题-->
    <p class="header">
      <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
    </p>
    <div class="search_publish" style="margin-top: 20px">
      <form action="javascript:return true;" class="input-kw-form">
        <input class="toggleSou" v-model="searchVal" value="55555555555555" type="search" autocomplete="off" placeholder="查询姓名/联系方式">
      </form>
      <mu-button icon slot="right" class="searchBtn" @click="this.downCallback">
        <mu-icon value="search"></mu-icon>
      </mu-button>
    </div>
    <!--滑动区域-->
    <mu-container ref="container" class="demo-loadmore-content">
    <div ref="mescroll" class="mescroll">
      <div v-if="noPerson">
        <img style="width: 100%;" src="../img/noFamily.png"/>
        <p style="text-align: center;font-size: 18px;margin-top: -10px">暂无患者</p>
        <mu-flex justify-content="center" @click="toAddPtient()" align-items="center" style="margin-top: 40px">
          <mu-button large  color="tealA700">添加患者</mu-button>
        </mu-flex>
      </div>
      <mu-list textline="three-line" v-if="havePerson">
         <template v-for="(i,index) in newArr">
           <!-- <mu-list-item>
              <mu-list-item-title>{{text}} Item {{i}}</mu-list-item-title>
            </mu-list-item>-->
            <mu-list-item avatar button :ripple="false">
              <mu-list-item-action>
                <!--<mu-avatar color="yellow600">
                  <mu-icon :value="insert_chart"></mu-icon>
                </mu-avatar>-->
                <img class="headimg" :src="i.fullHeadImgUrl">
              </mu-list-item-action>
              <mu-list-item-content>
                <mu-list-item-title>{{i.fullname}}  &nbsp;&nbsp;{{i.sex | sexFilter}}  &nbsp;&nbsp;{{i.age}}</mu-list-item-title>
                <mu-list-item-sub-title style="color: rgba(0, 0, 0, .87)"><a style="color: #42a5f5" :href="'tel:' + i.mobile">{{i.mobile}}</a></mu-list-item-sub-title>
                <mu-list-item-sub-title>
                  医生：{{i.doctors[0]}} &nbsp;&nbsp;&nbsp;&nbsp; 护士：{{i.nurses[0]}}
                </mu-list-item-sub-title>
              </mu-list-item-content>
              <mu-list-item-action >
                <!--<mu-icon class="toggle-icon" size="24" @click="caozuoWarn(index)" :color="index % 2 == 0 ? '' : 'yellow700'" :value="index % 2 == 0 ? 'alarm_add' : 'alarm_on'"></mu-icon>-->
                <!--<mu-checkbox color="yellow700" v-model="selects" value="value5"  uncheck-icon="alarm_add" checked-icon="alarm_on"></mu-checkbox>-->
                <mu-list-item-after-text><mu-button small color="primary" @click="patientDetail(i.id,i.fullname)">详细档案</mu-button></mu-list-item-after-text>
              </mu-list-item-action>
            </mu-list-item>
            <mu-divider />
          </template>
       <!-- <p class="notice">本Demo的下拉刷新: 添加新数据到列表顶部</p>-->
        <!--<ul id="newsList" class="news-list">
          <li v-for="news in newArr" :key="news.id">
            <p>{{news.title}}</p>
            <p class="new-content">{{news.content}}</p>
          </li>
        </ul>-->
      </mu-list>
    </div>
    </mu-container>
  </div>
</template>

<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
// 引入mescroll.min.js和mescroll.min.css
import MeScroll from 'mescroll.js'
import 'mescroll.js/mescroll.min.css'
import topheader from "../healthPageHome/topheader";
export default {
  name: 'listNews',
  data () {
    return {
      topName:'健康档案',
      backHrefValZhi:'/home',
      mescroll: null,
      newArr: [], // 数据列表
      searchVal:'',
      pageNumber:0,
      pageSize:15,
      noPerson:false,
      havePerson:true,
      isFirstEnter:false // 是否第一次进入，默认false
    }
  },
  mounted () {
    // 创建MeScroll对象:为避免配置的id和父组件id重复,这里使用ref的方式初始化mescroll
    this.mescroll = new MeScroll(this.$refs.mescroll, {// 在mounted生命周期初始化mescroll,以确保您配置的dom元素能够被找到.
      down: {
        auto: false, // 是否在初始化完毕之后自动执行下拉回调callback; 默认true
        callback: this.downCallback // 下拉刷新的回调
      },
      up: {
        auto: false, // 是否在初始化时以上拉加载的方式自动加载第一页数据; 默认false
        callback: this.upCallback, // 上拉回调,此处可简写; 相当于 callback: function (page) { upCallback(page); }
        page: {
          num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
          size: 10 // 每页数据的数量
        },
        noMoreSize: 5, // 如果列表已无数据,可设置列表的总数量要大于等于5条才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
        toTop: { // 配置回到顶部按钮
         // src: './../img/mescroll-totop.png'
        }
      }
    })
  },
  beforeRouteEnter (to, from, next) { // 如果没有配置回到顶部按钮或isBounce,则beforeRouteEnter不用写
    // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
    console.log(from.name);
    if(from.name == 'patientDetail'){
      to.meta.isBack = true;//是返回的 不需要请求数据，用缓存的
    }
    next(vm => {
      if (vm.mescroll) {
        // 恢复到之前设置的isBounce状态
        if (vm.mescroll.lastBounce != null) vm.mescroll.setBounce(vm.mescroll.lastBounce)
        // 滚动到之前列表的位置(注意:路由使用keep-alive才生效)
        if (vm.mescroll.lastScrollTop) {
          vm.mescroll.setScrollTop(vm.mescroll.lastScrollTop)
          setTimeout(() => { // 需延时,因为setScrollTop内部会触发onScroll,可能会渐显回到顶部按钮
            vm.mescroll.setTopBtnFadeDuration(0)// 设置回到顶部按钮显示时无渐显动画
          }, 16)
        }
      }
    })
  },
  beforeRouteLeave (to, from, next) { // 如果没有配置回到顶部按钮或isBounce,则beforeRouteLeave不用写
    if (this.mescroll) {
      this.mescroll.lastBounce = this.mescroll.optUp.isBounce// 记录当前是否禁止ios回弹
      this.mescroll.setBounce(true) // 允许bounce
      this.mescroll.lastScrollTop = this.mescroll.getScrollTop()// 记录当前滚动条的位置
      this.mescroll.hideTopBtn(0)// 隐藏回到顶部按钮,无渐隐动画
    }
    next()
  },
  methods: {
    backHrefVal(data){
      this.$router.push({path:data});
    },
    toBack(){
      this.$router.push({path:'/home'})
    },
    toAddPtient(){
      this.$router.push({path:'/addPatient',query:{name:this.searchVal}});
    },
    /* 下拉刷新的回调 */
    downCallback () {
      console.log('this.mescroll.version=' + this.mescroll.version);
      // 联网加载数据
      this.pageNumber = 1;
      let elThis = this;
      this.getListDataFromNet(1,this.pageSize, (data) => {
        // 添加新数据到列表顶部
        this.newArr = [];
        data.forEach(function (value) {
          elThis.newArr.push(value);
        });
        console.log(elThis.newArr);
        //elThis.newArr.unshift(data)
        // 数据渲染成功后,隐藏下拉刷新的状态
        this.$nextTick(() => {
          this.mescroll.endSuccess(data.length)// 结束下拉刷新,无参
        })
      }, () => {
        // 联网失败的回调,隐藏下拉刷新的状态
        this.mescroll.endErr()
      })
    },

    // 上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback (page) {
      console.log(page)
      this.pageNumber = this.pageNumber + 1;
      // 联网加载数据
      this.getListDataFromNet(this.pageNumber, this.pageSize, (curPageData) => {
        // 添加列表数据
        this.newArr = this.newArr.concat(curPageData)
        // 数据渲染成功后,隐藏下拉刷新的状态
        this.$nextTick(() => {
          this.mescroll.endSuccess(curPageData.length)
        })
      }, () => {
        // 联网失败的回调,隐藏下拉刷新和上拉加载的状态;
        this.mescroll.endErr()
      })
    },

    /* 联网加载列表数据
     在您的实际项目中,请参考官方写法: http://www.mescroll.com/api.html#tagUpCallback
     请忽略getListDataFromNet的逻辑,这里仅仅是在本地模拟分页数据,本地演示用
     实际项目以您服务器接口返回的数据为准,无需本地处理分页.
    * */
    getListDataFromNet (pageNum, pageSize, successCallback, errorCallback) {
      // 延时一秒,模拟联网
      console.log(pageNum)
        try {
          let elThis = this;
          let data = {};
          data.pageNumber = pageNum;
          data.pageSize = pageSize;
          data.keyword = this.searchVal;
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/all/patient', {params:data}).then((res) => {
            console.log(res);
            if (res.data.length == 0) {
              if (elThis.pageNumber == 1) {
                elThis.havePerson = false;
                elThis.noPerson = true;
              }
            } else {
              elThis.havePerson = true;
              elThis.noPerson = false;
              /* res.data.forEach(function (value) {
                 elThis.list.push(value);
               })*/
            }
            successCallback && successCallback(res.data || []);
          }).catch(function(err){
            ajaxError(err);
          })
        } catch (e) {
          // 联网失败的回调
          errorCallback && errorCallback()
        }
    },
    patientDetail(id,name){
      this.$router.push({
        path: '/patientDetail',
        query: {id: id,name:name}
      })
    }
  },
  filters:{
    sexFilter(value){
      if(value == 'man'){
        return '男'
      }else if(value == 'man'){
        return '女'
      }else{
        return '不详'
      }
    }
  },
  activated(){
    if(!this.$route.meta.isBack  || this.isFirstEnter){
     /* this.list = [];
      this.pageNumber = 1;*/
      //this.downCallback();
      this.searchVal = '';
      this.newArr =  [];
      this.downCallback();
    }
    // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
    this.$route.meta.isBack = false;
    this.isFirstEnter=false;
  },
  // 页面初始化
  created(){
    this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
  },
  components:{
    topheader
  }
}
</script>

<style scoped>
  /*以fixed的方式固定mescroll的高度*/
  .mescroll {
    position: fixed;
    top: 100px;
    bottom: 0;
    height: auto;
  }
  .header {
    z-index: 9990;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 44px;
    line-height: 44px;
    text-align: center;
    margin: 0;
    border-bottom: 1px solid #eee;
    background-color: white;
  }

  .header .btn-left {
    position: absolute;
    top: 0;
    left: 0;
    padding: 12px;
    line-height: 22px;
  }

  .header .btn-right {
    position: absolute;
    top: 0;
    right: 0;
    padding: 0 12px;
  }

  .mescroll .notice {
    font-size: 14px;
    padding: 20px 0;
    border-bottom: 1px solid #eee;
    text-align: center;
    color: #555;
  }

  .news-list li {
    padding: 16px;
    border-bottom: 1px solid #eee;
  }

  .news-list .new-content {
    font-size: 14px;
    margin-top: 6px;
    margin-left: 10px;
    color: #666;
  }
  .search_publish{
    width: 80%;
    position: relative;
    margin: 60px 10%  0 10%;
  }
  .toggleSou{
    border-radius: .35rem;
    font-size: .85rem;
    color: black;
    padding-left: 1em;
    height: 36px;
    line-height: 36px;
    margin-bottom: 7px;
    background-color: transparent;
    border: 1px solid #dddddd;
    outline: none;
    width: 100%;
  }
  .searchBtn{
    width: 1rem;
    position: absolute;
    top: .1rem;
    right: 4%;
    padding-top: .5rem;
    margin-top: -.5rem;
    padding-bottom: .5rem;
    margin-bottom: -.5rem;
  }
  .headimg{
    width: 50px;
    border-radius: 50%;
    border: 1px solid #dddddd;
    margin-right: 11px;
  }
</style>



