<template>
      <div class="homeDiv">
        <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
        <div class="mescroll1DivAdd">
          <div class="hos_child">
            <div class="hos_list_top clearfix">
              <div class="left">报告类型</div>
              <div class="right">
                <select class="community" v-model="community">
                  <option v-for="item in communityOption" :value="item.value">{{item.name}}</option>
                </select>
              </div>
            </div>
            <div class="hos_list_top clearfix">
              <div class="left">报告日期</div>
              <div class="right">
                <input id="bornDate" ref="button" @click="open = !open" v-model="dateResult" placeholder="请选择报告日期" type="text">
                <mu-popover cover :open.sync="open" placement="right" :trigger="trigger">
                  <mu-container>
                    <mu-flex justify-content="between" align-items="end" wrap="wrap">
                      <mu-paper :z-depth="1" class="demo-date-picker">
                        <mu-date-picker :date.sync="date" :max-date="maxDate"></mu-date-picker>
                      </mu-paper>
                    </mu-flex>
                    <button class="sureTime" @click="chooseTime">确认</button>
                  </mu-container>
                </mu-popover>
              </div>
            </div>
            <div class="hos_list_top clearfix">
              <div class="left">医疗机构</div>
              <div class="right"><input class="hosName" v-model="hosName" placeholder="请输入医疗机构名称" type="text"></div>
            </div>
          </div>
          <div class="orderBottom" style="height: inherit;">
            <!--	<p style="font-size: 1.03rem;line-height: 2.5rem;padding-left: 4%;">就诊病状</p>
                      <input class="bingzhuangInput" type="text" placeholder="请填写就诊病状"/>-->
            <p style="font-size: 1.03rem;line-height: 2.5rem;padding-left: 5.2%;color: #555;margin: 0">报告内容</p>
            <textarea placeholder="请填写报告内容、备注" v-model="breif"></textarea></div>
          <div class="orderBottom" style="height: inherit;padding-bottom: 1rem;    margin-top: -.5rem;">
					<span class="famousName orderNum inputImg">
            <p class="postImgP" v-if="imgs.length > 0" v-for='(item ,index ) in imgs'>
              <img class="closePhoto" v-if="closePhoto" @click="delPhoto(index)" src="../img/closePhoto.png"><img class="postImg" :src="item.imgUrl">
            </p>
						<span class="addPhontBtn" v-if="imgs.length < 6 && imgsBtn"><input type="file" class="hidden upload" @change='add_img' id="file" multiple="multiple">
							<img src="../img/addPhotoHui.png">
						</span>
					</span>
            <span v-if="warnPhoto" class="warnPhoto" :class="imgs.length >=1 ? 'havePhoto': ''">*请拍照上传体检报告，要求图片清晰</span>
          </div>
        </div>
        <button class="ordercommont postBingli" v-if="buttonShow" @click="addPatientReport">提交上传</button>
        <!--<div class="homeListTabOne">
          <ul class="clearfix">
            <li v-if="imgs.length>0" v-for='(item ,index ) in imgs'>
              <img :src="item">
            </li>
            <li style="position:relative" v-if="imgs.length>=6 ? false : true">
              <img src="../img/addPhoto.png"><input class="upload" @change='add_img' type="file">
            </li>
          </ul>
        </div>-->
      </div>
</template>
<script>
  import {get,post} from "../axios/ajax";
  import {ajaxError,formatDate} from "../assets/js/assist";
  import topheader from "../healthPageHome/topheader";
  import Vue from 'vue'
  export default {
      name:'postPhotos',
      // 组件

      data: function () {
        return {
          topName:'添加体检报告',
          backHrefValZhi:'/patientDetail',
          communityOption:[
            {name:'体检报告', value:'体检报告'},
          ],
          community:'体检报告',
          bornDate:'',
          hosName:'',
          breif:'',
          imgs: [],
          imgData: {
            accept: 'image/gif, image/jpeg, image/png, image/jpg',
          },
          open: false,
          trigger: null,
          date:undefined,
          dateResult:'',
          maxDate:new Date(),
          warnPhoto:true,
          buttonShow:true,
          imgsBtn:true,
          closePhoto:true
        }
      },
      mounted () {
        this.trigger = this.$refs.button.$el;
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data,query: {id: this.$route.query.id,name:this.$route.query.name}});
        },
        getReport(){
          let elThis = this;
          let data = '';
          let id = this.$route.query.reportId;
          get(this.SEVERADDR + '/health_supervisor/one/patientReport/' + id, {params:data}).then((res) => {
            console.log(res);
            this.dateResult = formatDate(new Date(res.data.recordTime),'yyyy-MM-dd');
            this.hosName = res.data.medicalInstitution;
            this.breif = res.data.content;
            res.data.imgs.forEach(function (value) {
              elThis.imgs.push(value);
              console.log(elThis.imgs)
            })
          }).catch(function(err){
            ajaxError(err);
          })
        },
        addPatientReport () {
          let elThis = this;
          let imgIds = [];
          elThis.imgs.forEach((value) => {
            imgIds.push(value.imgId);
          })
          console.log(imgIds.join(','))
          let data = {};
          data.type = 'medical_examination_report';
          data.medicalInstitution = this.hosName;
          data.content = this.breif;
          data.imgIds = imgIds.join(',');
          data.recordTime = this.dateResult + ' 00:00:00';
          console.log(data)
          let userId = this.$route.query.id;
          post(this.SEVERADDR + '/health_supervisor/'+userId+'/patientReport', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            if(res.success == true){
              this.$toast.success('添加成功');
            }else{
              this.$toast.error(res.data);
            }
            //this.$router.go(0);
            //elThis.loading1 = !elThis.loading1;
            // window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
        add_img(event){
          let reader =new FileReader();
          let img1=event.target.files[0];
          let type=img1.type;//文件的类型，判断是否是图片
          let size=img1.size;//文件的大小，判断图片的大小
          if(this.imgData.accept.indexOf(type) == -1){
            alert('请选择我们支持的图片格式！');
            return false;
          }
          if(size>3145728){
            alert('请选择3M以内的图片！');
            return false;
          }
          let uri = ''
          let form = new FormData();
          form.append('multipartFile',img1,img1.name);
         post(this.SEVERADDR + '/common/upload/img',form,{
            headers:{'Content-Type':'multipart/form-data'}
          }).then(response => {
            console.log(response.data)
            uri = response.data
            reader.readAsDataURL(img1);
            let that=this;
            reader.onloadend=function(){
              that.imgs.push(uri);
              console.log(that.imgs)
            }
          }).catch(error => {
            alert('上传图片出错！');
          })
        },
        delPhoto(index){
          console.log(index)
          this.imgs.splice(index,1);
        },
        chooseTime(){
          this.open = false;
          if(this.date == undefined || this.date == 'undefined'){
            this.dateResult = formatDate(new Date(),'yyyy-MM-dd');
          }else{
            this.dateResult = formatDate(this.date,'yyyy-MM-dd');
          }
        }
      },
    activated(){
        if(this.$route.query.reportId != undefined){
          this.warnPhoto = false;
          this.buttonShow = false;
          this.imgsBtn = false;
          this.closePhoto = false;
          this.imgs = [];
          this.getReport();
        }else{
          this.warnPhoto = true;
          this.buttonShow = true;
          this.imgsBtn = true;
          this.closePhoto = true;
          this.imgs = [];
          this.dateResult = '';
          this.hosName = '';
          this.breif = '';
        }

    },
      // 页面初始化
      created() {
        //console.log(this.$route.query.id);
      },
      components: {
        topheader
      }
    }
  Vue.filter('FormatData', function (value) {
    return  formatDate(new Date(value),'yyyy-MM-dd');
  })
</script>
<style scoped>
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #f0f0f0
  }
  .mescroll1DivAdd {
    background-color: white;
    margin-top: 55px;
  }
  .hos_child {
    /* height: 8rem; */
    padding-top: .2rem;
    padding-bottom: 0.2em;
    /* margin-top: .9rem; */
    float: left;
    margin-top: 2px;
  }
  .hos_child .hos_list_top {
    height: 2.9rem;
    /* border: 1px solid red; */
    font-size: 1rem;
    line-height: 2.5rem;
    /* display: flex; */
    float: left;
    width: 95%;
    color: #333333;
    margin-left: 2.8%;
    border-bottom: 1px solid #e5e5e5;
  }
  .hos_list_top .left {
    width: 25%;
    margin: -.2rem 0 0 -0.5%;
    line-height: 3.3rem;
    padding-left: 3%;
    color: #555;
    float: left;
  }
  .hos_list_top .right {
    padding-left: .2rem;
    margin-top: .2em;
    width: 71%;
    float: left;
  }
  .right select {
    width: 70%;
    height: 1.7rem;
    line-height: 1.7rem;
    border: 1px solid #c5c5c5;
    color: #666;
  }
  .right input[type="text"] {
    width: 90%;
    height: 2rem;
    line-height: 2rem;
    border: none;
    outline: none;
    font-size: .85rem;
  }
  .orderBottom {
    width: 100%;
    color: black;
    font-size: .8rem;
    margin: .5rem 0 0 0;
    height: 2.2rem;
    line-height: 1.6rem;
    clear: both;
    background-color: white;
  }
  .orderBottom textarea {
    width: 91%;
    border: 1px solid #d8d8d8;
    /* border: none; */
    font-size: .9rem;
    height: 7rem;
    resize: none;
    outline: none;
    padding: .5rem;
    margin: 0 0 0 4.5%;
    background-color: #f3f3f3;
  }
  .postImgP{
    width: 6rem;
    height: 6rem;
    line-height: 6rem;
    display: inline-block;
    text-align: center;
    border: 1px solid #e5e5e5;
    position: relative;
    margin-right: .7rem;
    vertical-align: top;
    margin-top: 1rem;
  }
  .famousName {
    display: inline-block;
    font-size: .92rem;
    margin-left: 4%;
  }
  .addPhontBtn {
    display: inline-block;
    width: 6rem;
    height: 6rem;
    /* border: 1px solid #dcdcdc; */
    text-align: center;
    vertical-align: top;
    margin-top: 1rem;
    position: relative;
    /* float: right; */
    margin-right: 0;
    background-color: #f1f1f1;
  }
  .addPhontBtn input[type="file"] {
    position: absolute;
    right: 4.3%;
    width: 6rem;
    height: 6rem;
    opacity: 0;
  }
  .addPhontBtn img {
    width: 1.8rem;
    margin: 2rem 0 -.2rem 0;
  }
  .postImg{
    max-width: 3.3rem;
    max-height: 3.3rem;
    margin: .1rem .1rem 0 .1rem;
  }
  .closePhoto{
    position: absolute;
    right: -.6rem;
    top: -.6rem;
    width: 1.3rem;
    background-color: white;
  }
  .warnPhoto {
    color: red;
    font-size: .9rem;
    vertical-align: top;
    display: inline-block;
    margin-top: 3rem;
  }
  .ordercommont {
    border-radius: 5px;
    color: white;
    font-weight: 500;
    font-size: .8rem;
    float: right;
    margin-left: 1.5%;
    margin-right: 3%;
    border: none;
    background-color: #00afa1;
    outline: none;
    padding: .4rem .7rem;
  }
  .postBingli {
    width: 80%;
    margin-left: 10%;
    height: 2.7rem;
    float: none;
    font-size: 1rem;
    margin-top: 2.7rem;
    margin-bottom: 2rem;
  }
  .sureTime {
    width: 50%;
    margin-left: 25%;
    height: 2.7rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 5px;
    color: white;
    font-weight: 500;
    border: none;
    background-color: #2196f3;
    outline: none;
    font-size: 18px;
    padding: .4rem .7rem;
  }
  .havePhoto{
    width: 100%;
    text-align: center;
    margin-top: 1rem;
  }

</style>



