<template>
  <div class="addPatient">
    <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>
    <div class="hos_child">
        <div class="hos_list_top clearfix" style="border: none;height: auto;">
          <div class="left" style="line-height: 1.3rem;margin-top: .8rem;">吸烟</div>
          <div class="right">
            <span class="yibaoPay"  v-for="(item,i) in smokeList" :class="{yibaoPayActive:item.bool}" @click="chooseSmokeOne(item,i,smokeList)">
              {{item.name}}
            </span>
            <br>
            <input v-if="xiyanIf" v-model="xiyanIfVal" class="haveSmoke" placeholder="日吸烟量平均几支" type="text">
            <input v-if="xiyanIf" v-model="xiyanIfAgeVal" class="haveSmoke" placeholder="开始吸烟年龄几岁" type="text">
            <input v-if="jieyanIf" v-model="jieyanIfVal" class="jieSmoke" placeholder="戒烟年龄几岁" type="text">
          </div>
        </div>
        <div class="hos_list_top clearfix" style="border: none;height: auto;">
          <div class="left" style="    line-height: 1.3rem;margin-top: .8rem;">饮酒</div>
          <div class="right">
            <span class="yibaoPay"  v-for="(item,i) in yinjiuList" :class="{yibaoPayActive:item.bool}" @click="chooseOne(item,i,yinjiuList)">
              {{item.name}}
            </span>
          </div>
        </div>
        <div class="hos_list_top clearfix" style="border: none;height: auto;">
          <div class="left" style="    line-height: 1.3rem;margin-top: .8rem;">锻炼情况</div>
          <div class="right">
            <span class="yibaoPay"  v-for="(item,i) in duanlianList" :class="{yibaoPayActive:item.bool}" @click="chooseOne(item,i,duanlianList)">
              {{item.name}}
            </span>
          </div>
        </div>
        <div class="hos_list_top clearfix">
          <div class="left">生活方式</div>
          <div class="right"><input class="userName" v-model="lifeFunc" placeholder="请输入其他生活方式"  type="text"/></div>
        </div>
        <div class="hos_list_top clearfix" style="border: none;height: auto;">
          <div class="left" style="    line-height: 1.3rem;margin-top: .8rem;">压力情况</div>
          <div class="right">
             <span style="padding: 0 .8rem;" class="yibaoPay"  v-for="(item,i) in yaliHave" :class="{yibaoPayActive:item.bool}" @click="chooseYaliOne(item,i,yaliHave)">
              {{item.name}}
            </span>
            <br>
            <span class="yibaoPay" v-if="yaliListShow === true"  v-for="(item,i) in yaliList" :class="{yibaoPayActive:item.bool}" @click="chooseYaliMany(item,i,yaliList)">
              {{item.name}}
            </span>
            <input class="qitaYali" v-if="qitaYali === true" v-model="qitaYaliVal" placeholder="请输入其他压力" type="text">
          </div>
        </div>
        <div class="hos_list_top clearfix" style="border: none;height: auto;">
          <div class="left" style="    line-height: 1.3rem;margin-top: .8rem;">饮食习惯</div>
          <div class="right">
             <span class="yibaoPay"  v-for="(item,i) in yinshiList" :class="{yibaoPayActive:item.bool}" @click="chooseMany(item,i)">
              {{item.name}}
            </span>
          </div>
        </div>
        <div class="hos_list_top clearfix" style="border: none;height: auto;">
          <div class="left" style="    line-height: 1.3rem;margin-top: .8rem;">睡眠情况</div>
          <div class="right" style="margin-bottom: 1rem;">
            <span class="yibaoPay"  v-for="(item,i) in shuimianList" :class="{yibaoPayActive:item.bool}" @click="chooseOne(item,i,shuimianList)">
              {{item.name}}
            </span>
          </div>
      </div>
    </div>
    <button class="bindBtn" @click="postUserPersonalLife">{{sureBtn}}</button>
  </div>

</template>
<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError} from "../assets/js/assist";
  import topheader from "../healthPageHome/topheader";
  let other = '其他';
  export default {
      name:'patientDetailLife',
      data: function () {
        return {
          topName:'个人生活方式',
          backHrefValZhi:'/patientDetail',
          lifeFunc:'',
          smokeList:[
            {name:'从不吸烟',bool:true},
            {name:'吸烟',bool:false},
            {name:'已戒烟',bool:false}
          ],
          xiyanIf: false,
          xiyanIfVal: '',
          xiyanIfAgeVal:'',
          jieyanIf: false,
          jieyanIfVal: '',
          yinjiuList:[
            {name:'从不',bool:true},
            {name:'偶尔',bool:false},
            {name:'经常',bool:false},
            {name:'每天',bool:false},
          ],
          duanlianList:[
            {name:'偶尔',bool:true},
            {name:'经常',bool:false},
            {name:'每天',bool:false},
            {name:'从不',bool:false},
          ],
          yaliHave:[
            {name:'无',bool:true},
            {name:'有',bool:false}
          ],
          yaliList:[
            {name:'记忆力变差',bool:false},
            {name:'青春痘',bool:false},
            {name:'情绪暴躁',bool:false},
            {name:'身上各种痛',bool:false},
            {name:'掉头发',bool:false},
            {name:'其他',bool:false}
          ],
          yaliListShow:false,
          qitaYali:false,
          qitaYaliVal:'',
          yinshiList:[
            {name:'荤素均衡',bool:false},
            {name:'荤食为主',bool:false},
            {name:'素食为主',bool:false},
            {name:'嗜盐',bool:false},
            {name:'嗜油',bool:false},
            {name:'嗜糖',bool:false},
            {name:'喜热',bool:false},
            {name:'冷饮',bool:false},
          ],
          shuimianList:[
            {name:'正常',bool:true},
            {name:'失眠',bool:false},
            {name:'浅眠',bool:false},
            {name:'深眠',bool:false},
          ],
          sureBtn:'确认',
          isFirstEnter:false // 是否第一次进入，默认false
        }
      },
      methods: {
        backHrefVal(data){
          this.$router.push({path:data,query:{id:this.$route.query.id,name:this.$route.query.name}});
        },
        chooseSmokeOne(item,i,list){
          list.forEach((value)=>{
            value.bool = false;
          });
          item.bool = !item.bool;
          if(item.name == '吸烟'){ //吸烟
            this.xiyanIf = true;
            this.jieyanIf = false;
          }else if(item.name == '已戒烟'){ //戒烟
            this.xiyanIf = true;
            this.jieyanIf = true;
          }else{ //不吸烟
            this.xiyanIf = false;
            this.jieyanIf = false;
          }
        },
        chooseYaliOne(item,i,list){
          list.forEach((value)=>{
            value.bool = false;
          });
          item.bool = !item.bool;
          if(item.name == '有'){ //有无压力
            this.yaliListShow = true;
            this.yaliList.forEach((value)=>{
              if(value.name == other && value.bool == true){
                this.qitaYali = true;
              }
            });
          }else{
            this.yaliListShow = false;
            this.qitaYali = false;
          }
        },
        chooseYaliMany(item,i,list){  //有其他压力
          console.log(item.name)
          item.bool = !item.bool;
          if(item.name == other && item.bool == true){
            this.qitaYali = true;
          }else if(item.name == other && item.bool == false){
            this.qitaYali = false;
          }
        },
        chooseOne(item,i,list){
          list.forEach((value)=>{
            value.bool = false;
          });
          item.bool = !item.bool;
        },
        chooseMany(item,i){
          item.bool = !item.bool;
        },
        getUserPersonalLife(){
          let elThis = this;
          let data = {};
          data.userId = this.$route.query.id;
          get(this.SEVERADDR + '/health_supervisor/userPersonalLife', {params:data}).then((res) => {
            if(res.success){
              //------------吸烟
              this.smokeList.forEach((value) => {
                value.bool = false;
                if(res.data.smoking.split('|').indexOf(value.name) != -1){
                  value.bool = true;
                  if(value.name == '吸烟'){
                    this.xiyanIf = true;
                    this.xiyanIfVal = res.data.daySmokingAvgCount;
                    this.xiyanIfAgeVal = res.data.startSmokingAge;
                  }else if(value.name == '已戒烟') {
                    this.xiyanIf = true;
                    this.jieyanIf = true;
                    this.xiyanIfVal = res.data.daySmokingAvgCount;
                    this.xiyanIfAgeVal = res.data.startSmokingAge;
                    this.jieyanIfVal = res.data.stopSmokingAge;
                  }
                }
              });
              this.lifeFunc = res.data.lifestyle;
              //------------饮酒
              this.yinjiuList.forEach((value) => {
                value.bool = false;
                if(res.data.drinking.split('|').indexOf(value.name) != -1){
                  value.bool = true;
                }
              });
              //------------锻炼
              this.duanlianList.forEach((value) => {
                value.bool = false;
                if(res.data.exerciseSituation.split('|').indexOf(value.name) != -1){
                  value.bool = true;
                }
              });
              //------------压力
              if(res.data.stressSituation && res.data.stressSituation != ''){
                console.log(this.yaliHave)
                this.yaliHave[0].bool = false;
                this.yaliHave[1].bool = true;
                this.yaliListShow = true;
                this.yaliList.forEach((value) => {
                  value.bool = false;
                  if (res.data.stressSituation.split('|').indexOf(value.name) != -1) {
                    value.bool = true;
                    if(value.name == '其他'){
                      this.qitaYali = true;
                    }
                    this.qitaYaliVal = res.data.stressSituation.split('|').pop();
                  }
                });
              }

              //------------饮食
              if(res.data.eatingHabits && res.data.eatingHabits != ''){
                this.yinshiList.forEach((value) => {
                  value.bool = false;
                  if(res.data.eatingHabits.split('|').indexOf(value.name) != -1){
                    value.bool = true;
                  }
                });
              }
               //------------睡眠
                this.shuimianList.forEach((value) => {
                  value.bool = false;
                if(res.data.sleepSituation.split('|').indexOf(value.name) != -1){
                  value.bool = true;
                }
              });
            }else{
              if(res.data != '无法获取此用户个人基本信息'){
                elThis.$toast.error(res.data);
              }
            }
          }).catch(function(err){
            ajaxError(err);
          })
        },
        postUserPersonalLife() {
          let elThis = this;
          let data = {};
          data.userId = this.$route.query.id;
          //------吸烟
          this.smokeList.forEach((value) => {
            if(value.bool == true){
              data.smoking = value.name;
              if(value.name == '吸烟'){
                data.daySmokingAvgCount = this.xiyanIfVal;
                data.startSmokingAge = this.xiyanIfAgeVal;
              }else if(value.name == '已戒烟') {
                data.daySmokingAvgCount = this.xiyanIfVal;
                data.startSmokingAge = this.xiyanIfAgeVal;
                data.stopSmokingAge = this.jieyanIfVal;
              }
            }
          });
          data.lifestyle = this.lifeFunc;
          //------饮酒
          this.yinjiuList.forEach((value) => {
            if(value.bool == true){
              data.drinking = value.name;
            }
          });
          //------锻炼
          this.duanlianList.forEach((value) => {
            if(value.bool == true){
              data.exerciseSituation = value.name;
            }
          });
          data.lifestyle = this.lifeFunc;
          //------压力
          let yaliList = [];
          if(this.yaliListShow == true){
            this.yaliList.forEach((value) => {
              if(value.bool == true){
                yaliList.push(value.name);
              }
            });
            if(this.qitaYali == true && this.qitaYaliVal != ''){
              yaliList.push(this.qitaYaliVal);
            }
            data.stressSituation = yaliList.join('|');
          }
          //------饮食
          let lastChooseYibao = [];
          this.yinshiList.forEach((value) => {
            if(value.bool == true){
              lastChooseYibao.push(value.name);
            }
          });
          data.eatingHabits = lastChooseYibao.join('|');
          //------睡眠
          this.shuimianList.forEach((value) => {
            if(value.bool == true){
              data.sleepSituation = value.name;
            }
          });
          console.log(data)
          post(this.SEVERADDR + '/health_supervisor/edit/userPersonalLife', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            this.$toast.success('个人生活方式完善成功');
            if(this.$route.query.write == true || this.$route.query.write == 'true'){
              this.$router.push({
                path: '/patientBodyInfo',
                query: {id:this.$route.query.id,name:this.$route.query.name,write:true}
              })
            }
            //window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
      },
    beforeRouteEnter(to, from, next) {
      // 所以，利用路由元信息中的meta字段设置变量，方便在各个位置获取。这就是为什么在meta中定义isBack
      console.log(from.name);
      if(from.name == 'patientDetail'){
        to.meta.isBack = false;//不是返回的，是进来要重新请求的
      }else{
        to.meta.isBack = true;//返回的
      }
      next();
    },
    activated(){
      if(this.$route.query.write == true || this.$route.query.write == 'true'){
        this.sureBtn = '下一步';
      }else{
        this.sureBtn = '确认';
      }
      if(!this.$route.meta.isBack  || this.isFirstEnter){
        this.getUserPersonalLife();
      }
      // 恢复成默认的false，避免isBack一直是true，导致下次无法获取数据
      this.$route.meta.isBack = false;
      this.isFirstEnter=false;
    },
      // 页面初始化
      created(){
        this.isFirstEnter = true;   // 只有第一次进入或者刷新页面后才会执行此钩子函数
      },
      components:{
        topheader
      }
    }
</script>
<style scoped>
  .addPatient{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #ebebeb;
  }
  .hos_child {
    margin-top: 53px;
    float: left;
    width: 100%;
    margin-left: 0;
    border-radius: 0;
    padding: 0 .2rem;
    padding-bottom: 0.2em;
    background-color: #ffffff;
  }
  .hos_child .hos_list_top {
    height: 2.9rem;
    /* border: 1px solid red; */
    font-size: 1rem;
    line-height: 2.5rem;
    /* display: flex; */
    float: left;
    width: 95%;
    color: #333333;
    margin-left: 2.8%;
    /* border-bottom: 1px solid #e5e5e5; */
  }
  .hos_list_top .left {
    width: 26%;
    margin: -.2rem 0 0 -0.5%;
    line-height: 3.3rem;
    padding-left: 3%;
    color: #555;
    float: left;
    font-size: .92rem;
  }
  .hos_list_top .right {
    padding-left: .2rem;
    margin-top: .2em;
    width: 70%;
    float: left;
  }
  .right input[type="text"] {
    width: 100%;
    height: 1.9rem;
    line-height: 2rem;
    border: 1px solid #bfbfbf;
    padding-left: 2%;
    outline: none;
    font-size: .85rem;
    border-radius: 3px;
  }
  .right select {
    width: 100%;
    height: 1.9rem;
    line-height: 1.7rem;
    border: 1px solid #a2a2a2;
    color: #5a5a5a;
    border-radius: 4px;
  }
  .sex:checked {
    background: url(../img/raido.png);
    background-size: 100% 100%;
    color: white;
    border: 0px;
  }
  .sex {
    float: left;
    width: 15px;
    margin-left: 12px;
    height: 15px;
    margin-top: .8rem;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background: #fff;
    border-radius: 50%;
    border: 1px solid #c0c0c0;
    color: #fff;
    display: block;
  }
  .sexname {
    float: left;
    margin: 0 2em 0 5px;
  }
  .yibaoPay{
    padding: 0 .4rem;
    height: 1.9rem;
    line-height: 1.9rem;
    border: 1px solid #c5c5c5;
    color: #555;
    font-size: .85rem;
    border-radius: 3px;
    display: inline-block;
    margin-right: .6rem;
  }
  .yibaoPayActive{
    background-color: #00afa1;
    color: white;
    border: 1px solid #00afa1;
  }
  .bindBtn {
    width: 52%;
    margin-left: 24%;
    border-radius: 5px;
    height: 2.5rem;
    line-height: 2.5rem;
    color: white;
    font-size: 1rem;
    margin-top: 6%;
    text-align: center;
    /* position: fixed; */
    bottom: 0;
    background-color: #00afa1;
    margin-bottom: 2rem;
    border: none;
  }
  .yibaoPayActive{
    background-color: #00afa1;
    color: white;
    border: 1px solid #00afa1;
  }
</style>



