<template>
  <c-layout-no-side>
    <!--<div>-->
     <!-- <table>
        <tr v-for="one in tableList">
          <td>{{one.area}}</td>
          <td>{{one.nick}}</td>
          <td>{{one.zkType}}</td>
          <td width="50%">{{one.title}}</td>
        </tr>
      </table>
    </div>-->
    <el-button
      size="mini"
      type="danger" ref="tableDel" @click="removeBatch(sels)" :disabled="this.sels.length === 0 || this.disabled">批量删除</el-button>
  <el-table
    :data="tableList"  @selection-change="selsChange"
    style="width: 100%">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
      prop="area"
      label="地址"
      width="180">
    </el-table-column>
    <el-table-column
      prop="nick"
      label="店铺名"
      width="180">
    </el-table-column>
    <el-table-column
      prop="zkType"
      label="促销">
    </el-table-column>
    <el-table-column
      prop="title"
      label="名称">
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">

        <el-button
          size="mini" @click="openSceneAddDialog">编辑</el-button>
        <el-tooltip content="确认删除此条？" placement="top">
        <el-button size="mini" type="danger" ref="table" @click="del(scope.row.item_id,scope.$index)">删除</el-button>
        </el-tooltip>
      </template>
    </el-table-column>
  </el-table>
  <el-dialog
    :title="sceneDialogTitle"
    :visible.sync="sceneDialogVisible"
    width="40%">
    <el-form label-width="80px">
      <el-form-item label="场景名称" prop="name">
        <el-input v-model="input.name"></el-input>
      </el-form-item>
      <el-form-item label="场景描述" prop="description">
        <el-input  v-model="input.desc"></el-input>
        <el-input  v-model="fullName"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="addAcount('newForm')">保 存</el-button>
      <el-button @click="resetForm1('newForm')">取 消</el-button>
    </div>
  </el-dialog>

  </c-layout-no-side>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "setlist",
      data(){
          return{
            sceneDialogTitle: '创建场景',
            sceneDialogVisible: false,
            firstName: '1',
            lastName: '2',
            sels: [],
            input:{
              name:'',
              desc:''
            },
            disabled:true
        }
      },
      props:{
        tableList:{
          type:Array
        }
      },
      computed: {   //两个中任意变化一个值 就会计算，数据未发生变化时，优先读取缓存     watch每次都去执行函数，并且要写多个
        fullName: function () {
          return this.input.desc + this.lastName
        }
      },
      watch: {
        firstName: function (val) {
          this.fullName = val + ' ' + this.lastName
        },
        lastName: function (val) {
          this.fullName = this.firstName + ' ' + val
        }
      },
      methods:{
        del(id,index){
          let index2 = index;
          console.log(index2)
          this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            var data = {};
            data.id = id;
            this.tableList.splice(index2,1);
             /* axios.post('/api/del', data).then((res) => {

              })*/
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        selsChange(sels){
          this.sels = sels;
          console.log(this.sels);
          this.disabled = false;
        },
        removeBatch(rows){
          console.log(rows)
          let ids = [];
          rows.forEach((ele)=>{
            console.log(ele)
            ids.push(ele.item_id)
          })
          console.log(ids);
          this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            let data = {};
            data.ids = ids;
            this.tableList.splice(index2,1);
            /* axios.post('/api/del', data).then((res) => {

             })*/
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        openSceneAddDialog () {
          //this.sceneForm = {id: '', name: '', description: ''}
          this.sceneDialogVisible = true;
          this.input.name = 'name';
          this.input.desc = 'desc';
        },
        resetForm1 () {
          this.sceneDialogVisible = false;
        },
      }
    }
</script>

<style scoped>
  table {
    width: 100%;
    font-size: 12px;
    border-collapse: collapse;
    margin: 0 auto;
    margin-right: 15px;
  }
  table td {
    text-align: center;
    min-height: 29.2px;
    line-height: 29.2px;
    border: 1px solid #dcdcdc;
  }
</style>



