<template>
  <div class="homeDiv">
   <!-- <topheader :headername="topName" :backHrefVal="backHrefValZhi" @backHrefVal="backHrefVal"></topheader>-->
    <img class="addMescroll1Img" @click="addJiuzhenBingli" src="../img/add.png">
    <mu-container ref="container" class="demo-loadmore-content">
    <!--  <mu-bottom-sheet :open.sync="open">
        <div class="btnBar"><div class="fixWidth"><div class="cancel" @click="closeBottomSheet">取消</div><div class="title">选择时间</div><div class="ensure" @click="sureBottomSheet">确认</div></div></div>
        <mu-flex justify-content="center" align-items="start" wrap="wrap">
          <mu-paper :z-depth="1">
            <mu-time-picker color="tealA700" :no-display="!display" :view-type="viewType" :time.sync="time" :format="type" :landscape="landscape"></mu-time-picker>
          </mu-paper>
        </mu-flex>
      </mu-bottom-sheet>-->
      <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
        <div id="mescroll3" class="mescroll">
          <div class="hos_child caseDiv set_box_shadow" v-for="i,index in list">
            <div class="hos_list_top clearfix">
              <div class="orderContent">
                <div class="left" style=""><img :src="i.imgUrl"></div>
                <div class="right"><p class="detail"><span class="name" style="font-size: 1.05rem">{{i.medicalInstitution}}</span>
                </p>
                  <p class="detail" style="line-height: 1rem;color: #999;    height: auto;"><span
                    class="name overflow_num_ellipsis">{{i.content}}</span></p>
                  <p class="orderdetailchoose" style="margin-bottom: .5rem;color: #999"><span>就诊时间：</span> <span
                    class="orderdetailchooseName">{{i.createDate | FormatData}}</span>
                    <button class="orderReadMore"  @click="readmore(i.id)">查看更多&gt;</button>
                  </p>
                </div>
              </div>
            </div>
          </div>
       <!--   <div v-for="i,index in list" class="hos_list_top clearfix">
            <div class="yuyueTop">
              <span class="famousName hosName">血压</span>
            </div>
            <div class="orderContent">
              <div class="left"><img src="../img/xueya.png"></div>
              <div class="rightList">
                <p class="detail">
                  <span class="name">高压 {{i.maximumPressure}}mmHg</span>
                </p>
                <p class="detail">
                  <span class="name">低压 {{i.minimumPressure}}mmHg</span>
                </p>
                <p class="detail" style="margin-bottom: .45rem;">
                  <span class="name">心率 {{i.heartRate}}mmHg</span>
                </p>
                <p class="orderdetailchoose" style="    margin-bottom: .5rem;">
                  <span>测量时间：</span>
                  <span class="orderdetailchooseName">{{i.monitoringDatetime}}</span>
                </p>
              </div>
            </div>
            <div class="clear"></div>
          </div>-->
        </div>
       <!-- <mu-list textline="three-line">
          <template v-for="i,index in list">
           &lt;!&ndash; <mu-list-item avatar button :ripple="false" style="    background-color: white;">
              <mu-list-item-action>
                <mu-avatar color="yellow600">
                  <mu-icon :value="insert_chart"></mu-icon>
                </mu-avatar>
              </mu-list-item-action>
              <mu-list-item-content>
                <mu-list-item-title>{{i.name}}</mu-list-item-title>
                <mu-list-item-sub-title style="color: rgba(0, 0, 0, .87)">{{i.hospital}}</mu-list-item-sub-title>
                <mu-list-item-sub-title>
                  {{i.diseaseLabel}}
                </mu-list-item-sub-title>
              </mu-list-item-content>
              <mu-list-item-action >
                <mu-icon class="toggle-icon" size="24" @click="caozuoWarn(index)" :color="index % 2 == 0 ? '' : 'yellow700'" :value="index % 2 == 0 ? 'alarm_add' : 'alarm_on'"></mu-icon>
             &lt;!&ndash;   <mu-checkbox color="yellow700" v-model="selects" value="value5"  uncheck-icon="alarm_add" checked-icon="alarm_on"></mu-checkbox>&ndash;&gt;
                <mu-list-item-after-text><mu-button small color="primary" @click="patientDetail(i.id,i.name)">详细档案</mu-button></mu-list-item-after-text>
              </mu-list-item-action>
            </mu-list-item>
            <mu-divider />&ndash;&gt;

          </template>
        </mu-list>-->
      </mu-load-more>
    </mu-container>
  </div>

</template>
<script>
  import {post,get} from "../axios/ajax";
  import {ajaxError,formatDate} from "../assets/js/assist";
  export default {
      name:'patientDetailCheckXueya',
      data: function () {
        return {
          maximumPressure:'',
          minimumPressure:'',
          heartRate:'',
          monitoringDatetime:'',
          open: false,
          time: undefined,
          landscape: false,
          display: false,
          type: '24hr',
          viewType: 'list',
          chooseDaytime:'',
          choosetime:'',
          list:[],
          refreshing: false,
          loading: false,
          insert_chart:'insert_chart',
          selects:true,
          searchVal:'',
          pageNumber:1
        }
      },
      methods: {
        addXueya () {
          let elThis = this;
          this.monitoringDatetime = this.chooseDaytime +' '+ this.choosetime + ':00';
          let data = {};
          data.userId = this.$route.query.id;
          data.maximumPressure = this.maximumPressure;
          data.minimumPressure = this.minimumPressure;
          data.heartRate = this.heartRate;
          data.monitoringDatetime = this.monitoringDatetime;
          console.log(data)
          post(this.SEVERADDR + '/health_supervisor/bloodPressure/addBloodPressure', this.$qs.stringify(data)).then((res) => {
            console.log(res)
            if(res.success == true){
              this.$toast.success('添加成功');
            }else{
              this.$toast.error(res.data);
            }
            //this.$router.go(0);
            //elThis.loading1 = !elThis.loading1;
           // window.location.reload();
          }).catch(function(err){
            ajaxError(err);
          })
        },
        getList(pageNumber){
          let elThis = this;
          let data = {};
          data.pageNumber = pageNumber;
          data.pageSize = 10;
          data.type = 'outpatient_cases';
          let userId = this.$route.query.id;
          console.log(data)
          get(this.SEVERADDR + '/health_supervisor/'+userId+'/patientReports', {params:data}).then((res) => {
            console.log(res);
            if(res.data.length > 0){
              res.data.forEach(function (value) {
                elThis.list.push(value);
              })
            }
            elThis.refreshing = false;
            elThis.loading = false;
          }).catch(function(err){
            ajaxError(err);
          })
        },
        refresh () {   //刷新，重新清空数组
          this.refreshing = true;
          this.pageNumber = 1;
          this.list = [];
          this.getList(this.pageNumber);
          this.$refs.container.scrollTop = 0;
        },
        load () {  //继续加载，push
          this.loading = true;
          this.pageNumber++;
          this.getList(this.pageNumber);
        },
        caozuoWarn(index){
          console.log(index)
          if(index % 2 == 0){

          }
        },
        patientDetail(id,name){
          this.$router.push({
            path: '/patientDetail',
            query: {id: id,name:name}
          })
        },
        addJiuzhenBingli(){
          this.$router.push({
            path: '/postPhotos',
            query: {id: this.$route.query.id,name:this.$route.query.name}
          })
        },
        sureBottomSheet(){
          this.open = false;
          if(this.time != undefined){
            this.choosetime = formatDate(this.time,'HH:mm');
          }
        },
        closeBottomSheet () {
          this.open = false;
        },
        openBotttomSheet () {
          this.open = true;
        },
        readmore(id){
          let ID = id;
          this.$router.push({
            path: '/postPhotos',
            query: {id: this.$route.query.id,name:this.$route.query.name,reportId:ID}
          })
        }
      },
      // 页面初始化
      created() {
        this.choosetime = formatDate(new Date(), 'HH:mm');
        this.chooseDaytime = formatDate(new Date(), 'yyyy-MM-dd');
        this.pageNumber = 1;
        this.getList(this.pageNumber);
      }
    }
</script>
<style scoped>
  .clear{
    clear: both;
  }
  .homeDiv{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #f1f1f1;
  }
  .hos_child {
    /* height: 8rem; */
    padding-top: .2rem;
    padding-bottom: 0.2em;
    margin-top: .9rem;
    float: left;
    background-color: white;
    width: 100%;
  }
  .set_box_shadow {
    width: 98%;
    position: relative;
    /* margin: 0 auto; */
    margin-left: 1%;
    margin-top: .9rem;
    border-radius: 5px;
    background-color: #ffffff;
    /* box-shadow: 0px 6px 20px 0 rgba(127, 127, 127, 0.19); */
    font-family: PingFang-SC;
    padding: 0 .2rem;
  }
  .hos_child .hos_list_top {
    /* height: 2.9rem; */
    /* border: 1px solid red; */
    font-size: .9rem;
    /* line-height: 2.5rem; */
    /* display: flex; */
    /* float: left; */
    width: 100%;
    color: #333333;
    /* padding-left: 2.8%; */
    /* border-bottom: 1px solid #e5e5e5; */
  }
  .orderContent {
    width: 96%;
    color: black;
    font-size: .85rem;
    height: 5.5rem;
    clear: both;
    margin: .5rem 0 0 2%;
    /* border-bottom: solid 1px #dcdcdc; */
  }
  .hos_list_top .left {
    width: 24%;
    line-height: inherit;
    margin: .1rem 0 0 -0.5%;
    padding-left: 0;
    color: #222;
    float: left;
  }
  .hos_list_top .left img {
    max-height: 4.9rem;
    overflow: hidden;
    width: initial;
    padding: .2rem;
  }
  .hos_list_top .right {
    width: 75%;
    float: left;
    margin-top: .1rem;
    padding-left: 2%
  }
  .right p {
    margin-bottom: .2rem;
    height: 1.4rem;
    margin-top: 0;
    line-height: 1.4rem;
  }
  .name, .sexImg {
    /* margin-right: 10px; */
  }
  .overflow_num_ellipsis {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  .orderReadMore {
    float: right;
    color: #00afa1;
    margin-top:0;
    background-color: white;
    border: none;
  }
  .addMescroll1Img {
    width:4rem;
    position: fixed;
    bottom: 0.8rem;
    right: 3%;
    z-index: 9;
  }
</style>



